//生成工具条上的工具 -- 生成dom字符串
function _generateToolDom(item, opt) {

    if (!item) {
        return ""
    }
    var res = '';
    // item.icon =  '../../image/toolbar/' + item.icon || '';
    // item.icon_hover = '../../image/toolbar/' + item.icon_hover || '';
    item.icon = item.icon || '';
    item.icon_hover = item.icon_hover || '';
    if (item.nodes && item.nodes.length > 0) {
        var childHtml = '';
        item.nodes.forEach(function (childNodes) {
            childHtml += _generateToolDom(childNodes, opt);
        })
        res = opt.conHasNodesTemplate.format(item, { nodes: childHtml });
    } else {
        res = opt.conTemplate.format(item);
    }

    return res;

}

//初始化control
async function initControl(id) {

    var self = this;
    var exOpt = self.options.exControlOpt.filter(x => x.id == id)[0] || {};
    // var controlDef = self.options.defControl.filter(x => x.id == id)[0];
    // var controlDef = getControlDef(id, self.options.defControl);


    var controlDef = getControlDef(id, self.__params.controls);
    if (!controlDef.url) {
        return null
    }
    // var _opt = control;
    var _id = controlDef.id || comLib.getRandom();

    var _tmpCon
    if (controlDef.loader) {
        _tmpCon = await controlDef.loader(controlDef.url);
    } else {
        _tmpCon = await getComponent(controlDef.url);
    }
    var control = comLib.isFunc(control) ? control : $.extend(true, {}, _tmpCon)

    control.init({
        ol: this.injects.ol,
        getMapHelper: this.injects.getMapHelper,
        mapContainer: this.injects.mapContainer,  // 地图所在div
        controlBarContainer: this.injects.controlBarContainer, //bar 所在的div
        controlBar: this.container // bar
    }, $.extend(true, {}, controlDef, exOpt.opt));
    control.id = _id;
    return control;

}

