//刷新bar的dom 把新添加的control画上去
function _refresh(forceRefresh, con, data) {

    var self = this
    var container = con || self.__params.container

    //强制刷新
    if (forceRefresh) {

        var controls = (data || self.__params.controls).map(x => {
            x.isNew = false
            //FIXME:
            // return comLib.isObj(x) ? x : defControl.filter(z => z.id == x)[0]
            return comLib.isObj(x) ? x : self.defaultControl.filter(z => z.id == x)[0]
        })
        var html = controls
            .map(x => _generateToolDom(x, self.options.templates))
        container.append(html);

    }
    //单个刷新 TODO: 改为一个更高效率的 能将临近项目一起合并更新 的方法
    else {
        var needAddItems = data || self.__params.needAppendDom;
        for (let i = 0; i < needAddItems.length; i++) {
            const item = needAddItems[i];
            item.isNew = false
            var beforeDom;
            var parentContainer
            if (item.parentID) {
                var menuContainer = container.find(`.control[data-control-id=${item.index}]`).siblings('.dropdown-menu');
                beforeDom = menuContainer.find(`.control[data-control-id=${item.beforeID}]`).parent('.controlBarItemContainer')
                parentContainer = menuContainer
            } else {
                beforeDom = container.find(`.control[data-control-id=${item.beforeID}]`).parent('.controlBarItemContainer')
                parentContainer = container
            }
            if (!beforeDom || beforeDom.length == 0) {
                parentContainer.append(item.html)
            } else {
                $(item.html).insertBefore(beforeDom)
            }

        }
    }

}

//组织需要添加的控件到__params.controls
function _addControl(controlsNeedToAdd, exisitControlList, asyncAppend) {
    var self = this;
    var __params = self.__params;
    var needAppendDom = __params.needAppendDom

    if (!exisitControlList) {
        exisitControlList = __params.controls
    }

    var templates = self.options.templates

    //先按index 从小到大排个序
    controlsNeedToAdd.sort(x => x.index)


    var res = [];

    var i = 0 // 
    var j = 0
    var exLength = exisitControlList.length
    var addLength = controlsNeedToAdd.length

    var _tmpNeedInsert = []
    var nextNeedAddControl = controlsNeedToAdd[j]
    nextNeedAddControl.isNew = true


    do {
        var exisitControl = exisitControlList[i];
        while (nextNeedAddControl && j < addLength && (!nextNeedAddControl.index || nextNeedAddControl.index <= i || nextNeedAddControl.index >= exisitControlList.length)) {
            //如果有指定父级就填到父级里            
            var parControl = getControlDef(nextNeedAddControl.parentID, exisitControlList);
            if (parControl) {
                //TODO: beforeID需要更高的效率
                var beforeID = undefined
                if (!parControl.nodes) {
                    parControl.nodes = [nextNeedAddControl]

                } else {
                    parControl.nodes.splice(nextNeedAddControl.index || 0, 0, nextNeedAddControl) //FIXME: 这个地方可能会有一个bug, 相同index按钮上的dom顺序会是其定义顺序的反向序列, 造成强制刷新的时候会出现按钮位置变动的bug
                    var oldNodes = parControl.nodes.filter(x => !x.isNew)
                    beforeID = oldNodes[nextNeedAddControl.index]
                }

                if (asyncAppend) {

                    needAppendDom.push({
                        id: nextNeedAddControl.id,
                        parentID: nextNeedAddControl.parentID,
                        beforeID: beforeID,
                        index: nextNeedAddControl.index,
                        html: _generateToolDom(nextNeedAddControl, templates)
                    })
                }
            }
            //如果没有指定父级, 那就是在root, 
            else {
                //没有index就放在最前
                if (!nextNeedAddControl.index) {
                    _tmpNeedInsert.push(nextNeedAddControl)
                } else {
                    res.push(nextNeedAddControl)
                }
                if (asyncAppend) {
                    needAppendDom.push({
                        id: nextNeedAddControl.id,
                        beforeID: exisitControl ? exisitControl.id : undefined,
                        index: nextNeedAddControl.index || 0,
                        html: _generateToolDom(nextNeedAddControl, templates)
                    })
                }


                //下边的条件好像是和while的重复了


                // //如果找到位置了,插入  //or 我的index都比原来大了,就直接添加到后边好了  这个条件好像
                // else if (nextNeedAddControl.index <= i || nextNeedAddControl.index >= exLength) {
                //     res.push(nextNeedAddControl)
                // }
            }

            j++
            nextNeedAddControl = controlsNeedToAdd[j];
            if (nextNeedAddControl) {
                nextNeedAddControl.isNew = true
            }
        }

        if (exisitControl) {
            res.push(exisitControl)
        }
        i++
    } while (i < exLength);
    res.splice.apply(res, [0, 0].concat(_tmpNeedInsert))



    __params.controls = res;


    return needAppendDom


}

//根据id获取control的定义obj
function getControlDef(id, controlDefList) {
    if (id == undefined) {
        return null;
    }
    var res;
    for (let i = 0; i < controlDefList.length; i++) {
        const def = controlDefList[i];
        if (def.id == id) {
            res = def
            break;
        }
        if (def.nodes) {
            res = getControlDef(id, def.nodes)
            if (res) {
                break;
            }
        }
    }
    return res
}

//绑定事件
function _bindEvents() {
    var self = this;
    var container = this.__params.container
    //control click
    container.delegate(".control", "click", function () {


        var $this = $(this);
        var id = $this.attr('data-control-id');

        self.injects.controlBarContainer.find('.groupContainer .dropdownArrow').removeClass(self.options.closeArrow).addClass(self.options.openArrow);
        self.injects.controlBarContainer.find('.groupContainer .dropdown-menu').removeClass('active');
        //如果是group点击
        if ($this.attr('isGroup')) {
            //如果已经打开就关闭
            if ($this.hasClass('isOpen')) {
                $this.removeClass('isOpen')

            } else {//如果是关闭的就展开下拉菜单
                $this.addClass('isOpen')
                $this.find('.dropdownArrow').removeClass(self.options.openArrow).addClass(self.options.closeArrow)
                $this.siblings('.dropdown-menu').addClass("active");
            }

        } else { //如果不是就触发control

            var control = self.getControl(id);
            self.activeControl(control || id);
        }

    });
    container.delegate(".control", "mouseover", function () {

        var $this = $(this);
        var _icon = $this.attr('data-icon');
        var _hovr = $this.attr('data-iconHover') || _icon;
        $this.find('.controlIcon').removeClass(_icon).addClass(_hovr);

    });
    container.delegate(".control", "mouseout", function () {

        var $this = $(this);
        var _icon = $this.attr('data-icon');
        var _hovr = $this.attr('data-iconHover') || _icon;
        $this.find('.controlIcon').removeClass(_hovr).addClass(_icon);
    });
}

