					this.xmbh = this.$route.params.xmbh || sessionStorage.getItem("xmbh");
					this.ToState = this.$route.params.ToState || sessionStorage.getItem("ToState");
					this.ToName = this.$route.params.ToName || "";
	
					let XBkeyword = this.$route.params.XBkeyword || sessionStorage.getItem("XBkeyword");

					(XBkeyword || XBkeyword != "") ? this.keyword = this.$route.params.XBkeyword: null;

					
					if(parseInt(this.ToState) == 3) {
						this.buttonDialog = true;
						this.infoVisible = false;
					} else {
						this.buttonDialog = false;
						this.infoVisible = true;
					}

					this.controlType = this.$route.params.type;
					

					this.getXiaoBanList(true);
					this.getXiaoBanLayers();

				}  

				         
			},
			Json() {
				console.log(this.Json.length);
				this.$store.commit("xiaoBanJson", this.Json.length);
				this.listData = this.Json.concat(this.serviceData);
			},
			filterText(val) {
				this.$refs.orgPersonTree.filter($.trim(val));
			},

		},
		created() {

		},
		mounted: function() {

			this.Height = document.body.clientHeight - 160;

			bus.$on("collapse", msg => {
				this.collapse = msg;
			});

			// 只有在标签页列表里的页面才使用keep-alive，即关闭标签之后就不保存到内存中了。
			bus.$on("tags", msg => {
				let arr = [];
				for(let i = 0, len = msg.length; i < len; i++) {
					msg[i].name && arr.push(msg[i].name);
				}
			});
			
			
			//map
			this.caculator = MapLibSW.caculator;
			let factory = MapLibSW.mapHelperFactory;
			let layers = MapLibSW.layers;
			let enums = MapLibSW.enums;
			let extensions = MapLibSW.extensions
			this.comLib = MapLibSW.comLib
			this.mapHelper = factory.createMapHelper();
			let eviaEx = new extensions.eviaEx({
				url: this.$config.eviaSearchUrl,
				translate: true,
				ignore: ["id", "gid"],
				date: "2009"
			}, {
				ol: ol
			});

			this.mapHelper.init({
				exBarOpt: [{
					id: enums.defBarId.mainTopRight,
					opt: {
						exControlOpt: [{
							id: enums.defControlId.identify,
							opt: {
								events: {
									exSearch: eviaEx.identify
								}
							}
						}]
					}
				}],
				// crateDefBarControl: false
				// layers: [layers.test()]
				target: "workMap",
				// spatialReference:'EPSG:3857',
			}, {
				ol: ol
			});

			//图层
			this.mapHelper.addLayer(enums.mapId.GoogleImage, layers.getGoogleImage());
			this.mapHelper.addLayer('evia', layers.getEviaLayer(null, null, this.$config.eviaLayer))
			this.map = this.mapHelper.map;

			this.xmbh = this.$store.state.xmbh || sessionStorage.getItem("xmbh");
			this.ToState = this.$route.params.ToState || sessionStorage.getItem("ToState");
			this.ToName = this.$route.params.ToName || "";


			parseInt(this.ToState) == 3? this.buttonDialog = true:this.buttonDialog = false;
			this.infoVisible = false;

			this.getXiaoBanList(true);
			this.getXiaoBanLayers();

			this.getDic(); //获取字典
			
			
			this.getRelations(); //获取表关系
		},
		methods: {
			tableRowClassName({
				row,
				rowIndex
			}) { //小班列表行className 运算

				if(row.id) {
					if(row.color == "success") {
						return 'success-row';
					} else if(row.state != this.ToState) {
						return 'green-row';
					} else {
						return '';
					}

				} else if(!row.id) {
					return 'warning-row';
				}
			},
			handleReachBottom() { //滚动至底部加载小班
				return new Promise(resolve => {
					let quest = {
						xmbh: this.xmbh,
						keyword: $.trim(this.keyword),
						pageQueryInfo: {
							pageSize: this.pageSize,
							pageIndex: this.pageIndex += 1
						},
					}
					this.$axios.post(this.$config.serverUrl + "api/YZL_ZLXB_PY/GetYZL_ZLXB_PYsByXmbh",
						JSON.stringify(quest), {
							headers: {
								'Content-Type': 'application/json'
							}
						}
					).then((res) => {
						//console.log(res.data);
						resolve();
						if(res.data.resultValue && res.data.resultValue.length > 0) {

							for(let obj of res.data.resultValue) {
								this.serviceData.push(obj);
							}
							//console.log(this.serviceData);
							this.listData = this.Json.concat(this.serviceData);
						} else {
							
							this.$message.warning("数据加载完毕");
							this.pageIndex -= 1;
						}
						
					}).catch(error => {
						this.$message.error(error);
					})

				});
			},
			change(status) { //改变抽屉的tap
				console.log(status);
			},
			renderContent(h, {
				node,
				data,
				store
			}) { //render  人员  的图标  和机构图标

				if(data.type == "person") {
					return <span> <i class="fa fa-child"></i>&nbsp;&nbsp;{data.name}</span>
				} else if(data.type == "org") {
					return <span><i class="fa fa-sitemap"></i>&nbsp;&nbsp;{data.name}</span>
				}
			},
			filterNode(value, data) { //筛选树节点
				if(!value) return true;
				return data.name.indexOf(value) !== -1;
			},
			nodeClick(ele, node) { //点击下发人员的树节点
				console.log(ele);
				console.log(node);
				if(ele.type == "person") {
					this.checkedPerson.name = ele.name;
					this.checkedPerson.id = ele.id;
					this.checkedPerson.org = node.parent.data.name;
					this.checkedPerson.orgId = node.parent.data.id;
				} else {
					this.checkedPerson.name = null;
					this.checkedPerson.id = null;
					this.checkedPerson.org = null;
					this.checkedPerson.orgId = null;
				}
			},
			handleSelectionChangeXB(value) { //多选小班列表的multipleSelection 赋值
				
				this.multipleSelectionXB = value;
			},
			formatterNumber(row, ele) { //小班列表的小班号展示
				return row.xbh || row.gid;
			},
			formatterEmploy(row, ele){
				
				let render=null;
				let _this = this;
				if(row.lockuserid_Name && row.lockUserId && row.lockuserid_Name !=""){
					
					if(row.xbh){  //不可更改指派人  暂定为xbh验证
						render = <span > 
									<span class="spanText">
										已下发给
									</span>
									<br/>
									<span 
									on-click={()=> _this.allotByPerson(row.id) }
									class="linkActive" title={row.lockuserid_Name} >
										{row.lockuserid_Name}
									</span>
								</span>
					}else{
						render  = <span > 
									<span title="任务正在进行中，不可更改" class="spanText disabled" >
										已下发给
									</span>
									<br/>
									<span  class="linkDisabled" title={row.lockuserid_Name}>
										{row.lockuserid_Name}
									</span>
								</span>
					}
					
				}else{  //没有被指派就可以指派
					
					render = <button title={ row.id ? "下发" : "提交后方可下发"}
									type="button" 
									disabled={!row.id ? "disabled" : null}
									class="ivu-btn ivu-btn-success ivu-btn-circle ivu-btn-small ivu-btn-icon-only"
									on-click={()=> _this.allotByPerson(row.id) }
									>
								<i class="ivu-icon ivu-icon-md-return-right"></i> 
							</button>
				}
				
				return render;
			},
			allotByPerson(xbid){    //通过点击人员下发
				console.log(xbid);
				for(let i= 0 ;i<this.listData.length;i++){
					if(this.listData[i].id == xbid){
						 this.$refs.multipleTable.clearSelection();
						 this.$refs.multipleTable.toggleRowSelection(this.listData[i], true);
						 this.allotDialog();
						 break;
					}
				}
			},
			selectable(row,index){   //多选框是否可选
				if(row.id){
					return true;
				}else{
					return false;
				}
			},
			getDic() { //获取小班所需要的所有字典
				this.$store.dispatch("getDics",["LANDPREPARATION",
					"SITEPREPARATION",
					"MIXED",
					"LANDFORM",
					"GENTLE",
					"PODU",
					"PW",
					"POXIANG",
					"LANDUSEDATE",
					"TREE",
					"LDLX",
					"PLANTING"
				]).then(data=>{
	            	for(let i = 0; i < data.length; i++) {
								switch(data[i].type) {
									case "LANDPREPARATION":
										this.zdfsOpt = data[i].data;
										break;
									case "SITEPREPARATION":
										this.zdplfsOpt = data[i].data;
										break;
									case "MIXED":
										this.hjfsOpt = data[i].data;
										break;
									case "LANDFORM":
										this.diMaoOpt = data[i].data;
										break;
									case "GENTLE":
										this.diLeiOpt = data[i].data;
										break;
									case "PODU":
										this.poDuOpt = data[i].data;
										break;
									case "PW":
										this.poWeiOpt = data[i].data;
										break;
									case "POXIANG":
										this.poXiangOpt = data[i].data;
										break;
									case "LANDUSEDATE":
										this.tdsyqOpt = data[i].data;
										break;
									case "TREE":
										this.shuZhongOpt = data[i].data;
										break;
									case "PLANTING":
										this.zaiZhiOpt = data[i].data;
										break;
									case "LDLX":
										this.LDLXOpt = data[i].data;
										break;
								}
							}
	            })
			},
			getRelations() { //表关系   主要是 造林模式，，，树种设计的对外表关系
				let tableKey = [{
					sourceTable: "YZL.YZL_JJZB",
					targetTable: "YZL.YZL_ZLXB_PY"
				}, {
					sourceTable: "YZL.YZL_ZLMS",
					targetTable: "YZL.YZL_ZLXB_PY"
				}]
				this.$axios
					.post(this.$config.serverUrl + "api/TableRelations/GetTableRelationsByTargetInfo",
						JSON.stringify(tableKey), {
							headers: {
								"Content-Type": "application/json"
							}
						})
					.then(res => {
						console.log(res);

						let FD = {};
						let EI = {};
						for(let obj of res.data) {

							if(obj.sourceTable == "YZL.YZL_ZLMS") {
								FD[obj.targetField] = obj.sourceField
							}
							if(obj.sourceTable == "YZL.YZL_JJZB") {
								EI[obj.targetField] = obj.sourceField
							}
						}
						this.relationData.fromEcoicIndex = EI;
						this.relationData.fromForestMode = FD;

						console.log(this.relationData);
					})
					.catch(error => {
						this.$message.error(error);
					});
			},
			getXiaoBanList(init) { //得到小班的列表信息，，第一次展示15条，，后面每次+5  均可配置

				let quest = {
					xmbh: this.xmbh,
					keyword: $.trim(this.keyword),
					pageQueryInfo: {
						pageSize: init ? 15 : this.pageSize,
						pageIndex: init ? 1 : this.pageIndex
					},
				}
				console.log(JSON.stringify(quest));
				return this.$axios.post(this.$config.serverUrl + "api/YZL_ZLXB_PY/GetYZL_ZLXB_PYsByXmbh",
					JSON.stringify(quest), {
						headers: {
							'Content-Type': 'application/json'
						}
					}
				).then((res) => {
					console.log(res.data);
					this.loaing = false;
					if(res.data.resultValue && res.data.resultValue.length > 0) {
						this.serviceData = res.data.resultValue;
						this.listData = this.Json.concat(this.serviceData);
						if(this.addNum > 0) {
							for(let i = 0; i < this.addNum; i++) {
								this.listData[i].color = "success";
							}
						}
					} else {
						this.$message.error("暂无数据");
						this.serviceData = [];
						this.listData = [];
					}
				}).catch(error => {
					this.$message.error(error);
				})
			},
			getXiaoBanLayers(saveAllLayer) { //获取该项目下所有的小班，并绘制在地图上  saveAllLayer 是否保留之前的所有的layer   默认不保存,全部清空
				let quest = {
					xmbh: this.xmbh,
					keyword: $.trim(this.keyword),

				};
				let _this = this;
				let urlStr = `http://39.105.11.71:8081/geoserver/smartForest/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=smartForest:zaolinyewu&maxFeatures=50&outputFormat=application%2Fjson`+
								`&viewparams=XMBH:'${quest.xmbh}';XBH:%27%25${quest.keyword}%25%27&tdsourcetag=s_pctim_aiomsg`;			
				let vectorLayer = new ol.layer.Vector({//初始化矢量图层
								source: new ol.source.Vector({
										format: new ol.format.GeoJSON({
											geometryName: 'Shape' //geometry 
										}),
										url:urlStr
									}),
								style: new ol.style.Style({
											fill: new ol.style.Fill({
												color: this.layerColor.basic.fillColor,
											}),
											stroke: new ol.style.Stroke({
												color: this.layerColor.basic.borderColor,
												width: 2
											})
										})
								});
					!saveAllLayer ? this.cleanMap("all") : null;
					this.serviceLayer = vectorLayer;
					this.map.addLayer(vectorLayer);
					return Promise.resolve();
			},
			searchData() { //根据keyword 小班号 xhb 搜索小班
				console.log("keyword");
				this.addNum = 0;
				this.getXiaoBanList(true);
				this.getXiaoBanLayers();
			},
			rowDblclick(row,event){
				
				let rowIndex = -1;
				for(let i=0;i<this.listData.length;i++){
					if(this.listData[i].id == row.id){
						rowIndex = i;
						break;
					}
				}

				this.XiaoBanInfo(row,rowIndex);
			},
			XiaoBanInfo(data,rowIndex) { //查看某个小班的属性信息，和地图上的定位，高亮显示
				console.log(data);
				//没有小班号。，，，面积查图层
                
				let fillColor = this.layerColor.activeGreen.fillColor; //淡绿
				let borderColor = this.layerColor.activeGreen.borderColor; //淡绿

				if(data.state != this.ToState) { //红
					fillColor = this.layerColor.activeRed.fillColor;
					borderColor = this.layerColor.activeRed.borderColor;
				}

				if(data.shape.coordinates) {


					let polygon =new ol.geom.MultiPolygon();
					 polygon.setCoordinates(data.shape.coordinates);
					 
					//polygon.setCoordinates(this.mapHelper.transForm(data.shape.coordinates))
					// let a = new ol.geom.MultiPolygon();
					// let m = new ol.geom.MultiPolygon();
					// m.setCoordinates(data.shape.coordinates);
					// a.setCoordinates(data.shape.coordinates);

					// a = this.mapHelper.toProj(a);
					this.mapHelper.zoomToFeature(polygon);

					//写入小班数据  公顷转换成亩  
					//如果没有原值就计算
					let area;

					if(!data.computed){
						if(!this.comLib.isNumbr(data.xbmj)) {
						
							var polygonTrans = this.mapHelper.transForm(polygon,null,'EPSG:4326')
							area = this.caculator.getArea(polygonTrans);						

							data.xbmj = parseFloat(area * 10000 / 666.6666667).toFixed(2);
						
						} else {

							data.xbmj = parseFloat(data.xbmj * 10000 / 666.6666667).toFixed(2);
						}

						if(typeof(data.zlmj) != "number") {

							data.zlmj = data.xbmj;

						} else {

							data.zlmj = parseFloat(data.zlmj * 10000 / 666.6666667).toFixed(2);
						}
					}
					
					this.listData[rowIndex].computed = true;   //表示该小班面积和造林面积已经由公顷转换成了亩
					
					if(data) {
						
						//设置小班数据的  值转换 字符串转数字，，，便于与字典对应上		
						
						let _this = this;
						let setXiaoBanData = function(){
							
							_this.XiaoBanData = data;
							_this.podu += "";				 //原来是 num 转成string 与字典一致
							
							if(_this.XiaoBanData.zlmsh){
								isNaN(_this.XiaoBanData.zlmsh) ? _this.XiaoBanData.zlmsh = null : 
															_this.XiaoBanData.zlmsh = parseInt(_this.XiaoBanData.zlmsh);
							}else{
								_this.XiaoBanData.zlmsh = null ;
							}
							
							if(_this.XiaoBanData.biaozhun){
								isNaN(_this.XiaoBanData.biaozhun) ? _this.XiaoBanData.biaozhun = null :
															_this.XiaoBanData.biaozhun = parseInt(_this.XiaoBanData.biaozhun);
							}else{
								_this.XiaoBanData.biaozhun = null ;
							}								
							
							
							
						}
						
						//获取对应的模式  经济指标 默认去掉典型图示的展示，有的话就展示
						this.carousel = false;
						this.carouselArr=[];
						
						if(data.ldlx){
							this.getZLMS(data.ldlx).then(()=>{
								if(data.zlmsh){
									
									//根据造林模型号获取典型图示
									for(let obj of this.modelData) {
										if(obj.data.id == data.zlmsh) {
											console.log(obj);
											if(obj.path && obj.path.length >0) { //如果没有图片
												this.carouselArr=obj.path;
												this.carousel = true;
											}
										}
									}
									
									this.getEcoicIndex(data.zlmsh).then(()=>{
										
										setXiaoBanData();
										
									})
									
									
									
									
								}else{
									setXiaoBanData();
									
								}
							})
						}else{
							setXiaoBanData();
						}
						
					} else {
						this.XiaoBanData = {};
					}
					////////
					let feature = {
						type: "Feature",
						geometry: data.shape,
						// geometry:polygonTrans.getGeometry(),
						id: data.id
					};

					let geojsonObject = {
						type: "FeatureCollection",
						crs: {
							type: "name",
							properties: {
								name: "EPSG:4326"
							}
						},
						features: [feature]
					};

					let vectorSource = new ol.source.Vector({
						features: new ol.format.GeoJSON({
							// featureProjection: "EPSG:3857"
						}).readFeatures(geojsonObject)
					});
					let vectorLayer = new ol.layer.Vector({
						//初始化矢量图层
						source: vectorSource,
						style: new ol.style.Style({
							fill: new ol.style.Fill({
								// color: "red"
								color: fillColor,
							}),
							stroke: new ol.style.Stroke({
								color: borderColor,
								width: 2
							})
						})
					});

					this.activeLayer ? this.map.removeLayer(this.activeLayer) : null;
					this.activeLayer = vectorLayer;
					this.map.addLayer(this.activeLayer);
				}

				bus.$emit('collapse', true);
				this.xiaoBanInfoVisible = true;
				
				//存入   原来的小班模型号   保存时，是否被删除
				this.oldModelId =data.zlmsh;
				//用过 小班id 查询 对应树种
				this.$axios.post(
						this.$config.serverUrl + "api/YZL_ZLXB_PY/GetFL_RGXB_XBSZsByXbId",
						JSON.stringify(data.id), {
							headers: {
								"Content-Type": "application/json"
							}
						}
					)
					.then(res => {
						console.log(res);
						this.ShuZhongData  = res.data;
						if(this.ShuZhongData.length>0){
							this.oldShuZhongArr = [];
							for(let ele of this.ShuZhongData){
								this.oldShuZhongArr.push(ele.id);
							}
						}else{
							this.oldShuZhongArr = [];
						}
					})
					.catch(error => {
						this.$message.error(error);
					})
				
				//根据对应标准得到树种价格  //可为null
				this.getShuZhongPriceData(data.biaozhun);
				
				
			},
			addShuZhong() { //给小班添加树种
				this.ShuZhongData.push({
					shuzhong: null,
					xbid: this.XiaoBanData.id,
					zzff: null,
					mmgg: null,
					hjbl: null,
					zlmd: null,
					zdgg: null,
					zdfs: null,
					zmliang: null,
				})
			},
			removeShuZhong(index) {
				console.log(index);
				this.$confirm('是否删除该树种？', '提示', { //自己的临时图层
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {

					this.ShuZhongData.splice(index, 1);

				}).catch((error) => {
					if(error == "cancel") {
						this.$message({
							type: 'info',
							message: "已取消"
						});
					} else {
						this.$message({
							type: 'info',
							message: error
						});
					}

				});

			},
			saveShuZhong() {
				
				if(this.XiaoBanData.id){
					
					if(this.oldModelId == this.XiaoBanData.zlmsh){  //原来的模型号和现在的相同 不保存
						
					}else{     //不相同的话，，删除原来的   添加新的   可同时进行
						
						if(this.oldShuZhongArr.length > 0){  //原的  且不为空  就删除
							
							this.$axios.post(this.$config.serverUrl + "api/YZL_ZLXB_PY/DeleteFL_RGXB_XBSZs",
								JSON.stringify(this.oldShuZhongArr), {
									headers: {
										'Content-Type': 'application/json'
									}
								}
							).then((res) => {
								console.log(res);
			
							}).catch(error => {
			
								this.$message.error(error);
							})
						
							
						}
						
						
						//添加新的树种
						let shuZhongArr = [];
						for(let ele of this.ShuZhongData){
							ele.xbid  = this.XiaoBanData.id ;
							delete ele.id;   //去掉id  都是增加，，没有更新
							shuZhongArr.push(ele);
						}
						let data= {
							fL_RGXB_XBSZs : shuZhongArr,
							token : sessionStorage.getItem("token"),
						}
						console.log(JSON.stringify(data));
						
						
						this.$axios.post(this.$config.serverUrl + "api/YZL_ZLXB_PY/AddOrUpdateFL_RGXB_XBSZs",
							JSON.stringify(data), {
								headers: {
									'Content-Type': 'application/json'
								}
							}
						).then((res) => {
							console.log(res);
		
						}).catch(error => {
		
							this.$message.error(error);
						})
						
					}
					
					
					
				}else{
					this.$message.error("小班数据错误，请重新选择");
				}
				
			},
			saveXiaoBan(name, type) { // 用于修改小班信息后保存，或者提交
				
				//1.操作保存小班信息
				//2，保存树种   *删除原来的，，，，保存现在的   ||  只保存现在的
				  
				
				this.$refs[name].validate((valid) => {
					if(valid) {

						let value = this.XiaoBanData;
						value.xmbh = this.xmbh;

						if(type == "toNext") {

							value.state = this.ToState;
						}

						value.token = sessionStorage.getItem("token");

						//亩转换为公顷
						if(value.computed){
							value.xbmj = parseFloat(value.xbmj * 666.66667 / 10000).toFixed(2);
							value.zlmj = parseFloat(value.zlmj * 666.66667 / 10000).toFixed(2);
							
						}
						
						console.log(JSON.stringify(value));

						this.saveShuZhong();
						this.$axios.put(this.$config.serverUrl + "/api/YZL_ZLXB_PY/PutYZL_ZLXB_PY/" + this.XiaoBanData.id,
							JSON.stringify(value), {
								headers: {
									'Content-Type': 'application/json'
								}
							}
						).then((res) => {
							console.log(res);
							this.getXiaoBanList(true);
							this.$message.success("保存小班成功");
						}).catch(error => {

							this.$message.error(error);
						})
						
						
					} else {
						this.$Message.error('请正确填写');
					}
				})
				
				
			},

			cleanMap(scope) { //清除地图上的对应图层  

				switch(scope) {

					case "all": //清楚所有图层，，所有选择操作

						this.activeLayer ? this.map.removeLayer(this.activeLayer) : null;

						this.serviceLayer ? this.map.removeLayer(this.serviceLayer) : null;

						this.boxLayer ? this.map.removeLayer(this.boxLayer) : null;
						this.dragBox ? this.map.removeInteraction(this.dragBox) : null;

						this.drawedFeature = [];
						this.drawLayer ? this.map.removeLayer(this.drawLayer) : null;
						this.drawInteraction ? this.map.removeInteraction(this.drawInteraction) : null;
						break;

					case "draw": //清楚绘制操作

						this.drawInteraction ? this.map.removeInteraction(this.drawInteraction) : null;
						this.drawedFeature = [];
						this.drawLayer ? this.map.removeLayer(this.drawLayer) : null;

						break;
					case "box":

						this.boxLayer ? this.map.removeLayer(this.boxLayer) : null;
						this.dragBox ? this.map.removeInteraction(this.dragBox) : null;//FIXME: 清除可能会造成反选工具失效

						break;
					case "cancelBox":

						this.dragBox ? this.map.removeInteraction(this.dragBox) : null;

						break;
					case "service":

						this.serviceLayer ? this.map.removeLayer(this.serviceLayer) : null;

						break;
					case "active":

						this.activeLayer ? this.map.removeLayer(this.activeLayer) : null;

						break;
				}
			},
			delDataForMap(data) { //原来的全部删除，，，然后全部重新添加    针对框选的图层
				//先干掉之前所有的boxLayer  然后再添加
				this.cleanMap("box");
				//图层上消失
				let json = data;
				let features = [];
				for(let i = 0; i < json.length; i++) {

					let geojson = JSON.parse(json[i].geojson);
					let feature = {
						type: "Feature",
						geometry: geojson,
						id: json[i].gid
					};
					features.push(feature);
				}
				let geojsonObject = {
					type: "FeatureCollection",
					crs: {
						type: "name",
						properties: {
							name: "EPSG:4326"
						}
					},
					features: features
				};
				let vectorSource = new ol.source.Vector({
					features: new ol.format.GeoJSON({
					//	featureProjection: "EPSG:3857"
					}).readFeatures(geojsonObject)
				});

				let _this = this;
				let vectorLayer = new ol.layer.Vector({
					//初始化矢量图层
					source: vectorSource,
					style: new ol.style.Style({
						fill: new ol.style.Fill({
							color: _this.layerColor.draw.fillColor,
						}),
						stroke: new ol.style.Stroke({
							color: _this.layerColor.draw.borderColor,
							width: 2
						})
					})
				});

				this.boxLayer = vectorLayer;
				this.map.addLayer(vectorLayer);
			},
