				const scrollList = this.$refs.scrollList;
				if(scrollList.scrollTop + document.body.clientHeight >= scrollList.scrollHeight){
					console.log("------");
				}
			},
			selectDr(name) {
				console.log(name);
			},
			handleSelect(command){   //框选下拉列表handle
				switch(command){
					case "selected" :
					this.selectTiff();
					break;
					case "unSelect" :
					this.unselectTiff();
					break;
					case "cleanSelect" :
					this.cleanMap('cancelBox');
					break;
				}
			},
			selectTiff() {

				let self = this;
				//框选添加资源小班  操作this.Json
				self.features = [];
				
				this.map.removeInteraction(this.dragBox);
				let select = new ol.interaction.Select();
				this.map.addInteraction(select);

				let selectedFeatures = select.getFeatures();

				let dragBox = new ol.interaction.DragBox({

				});

				this.map.addInteraction(dragBox);

				
				let token = sessionStorage.getItem('token');
				dragBox.on("boxstart", function() {});
				dragBox.on("boxend", function() {

					let extent = dragBox.getGeometry().getExtent();
					extent = self.mapHelper.transForm(extent,self.mapHelper.spatialReference,"EPSG:4326");


					let pointmin = {
						x: extent[0],
						y: extent[1]
					};
					let pointmax = {
						x: extent[2],
						y: extent[3]
					};
					
					let urlq =
	
						self.$config.eviaSearchSource+"?lat1=" +
						pointmin.y +
						"&lng1=" +
						pointmin.x +
						"&lat2=" +
						pointmax.y +
						"&lng2=" +
						pointmax.x +
						"&years=2009&ignore=id,wkt";
					$.getJSON(urlq, function(data) {

						if(data.status != "false") {
							let json = data.info;
							let features = [];
							for(let i = 0; i < json.length; i++) {
								if(self.Json.findIndex((element) => (element.gid == json[i].gid)) == -1) {

									json[i].token = token;
									json[i].xmbh = self.xmbh;
									json[i].state = 2; //默认都是没有提交的状态
									// 	json[i].xbh = json[i].xian+"_"+json[i].xiao_ban;
									json[i].xbh = `${json[i].xian}_${json[i].xiao_ban}`;
									self.Json.push(json[i]);
								}
								let data = JSON.parse(json[i].geojson);

								let feature = {
									type: "Feature",
									geometry: data,
									id: json[i].gid
								};
								//去重
								if(self.features.findIndex((element) => (element.id == feature.id)) == -1) {
				
									self.features.push(feature);

								}

							}
							//self.features.push(features);
							let geojsonObject = {
								type: "FeatureCollection",
								crs: {
									type: "name",
									properties: {
										name: "EPSG:4326"
									}
								},
								features: self.features
							};
							let vectorSource = new ol.source.Vector({
								features: new ol.format.GeoJSON({
									// featureProjection: "EPSG:3857"
								}).readFeatures(geojsonObject)
							});
							let vectorLayer = new ol.layer.Vector({
								//初始化矢量图层
								source: vectorSource,
								style: new ol.style.Style({
									fill: new ol.style.Fill({
										color: self.layerColor.draw.fillColor,
									}),
									stroke: new ol.style.Stroke({
										color: self.layerColor.draw.borderColor,
										width: 2
									})
								})
							});
							//    self.map.removeLayer(self.vectorLayer);
							self.boxLayer ?  self.map.removeLayer(self.boxLayer) :null;
							self.boxLayer = vectorLayer;
							self.map.addLayer(self.boxLayer);

						} else {
							self.$message.error(data.info);
						}

					});
				});
				this.dragBox = dragBox;
			},
			unselectTiff() { //框选取消资源小班  操作this.Json
				let self = this
				let map = this.map;
				this.map.removeInteraction(this.dragBox);
				let select = new ol.interaction.Select();
				this.map.addInteraction(select);

				let selectedFeatures = select.getFeatures();

				let dragBox = new ol.interaction.DragBox({});

				this.map.addInteraction(dragBox);				
				dragBox.on("boxstart", function() {
					selectedFeatures.clear();
				});
				dragBox.on("boxend", function() {
					let extent = dragBox.getGeometry().getExtent();
					extent = self.mapHelper.transForm(extent,self.mapHelper.spatialReference,"EPSG:4326");
					let pointmin = {
						x: extent[0],
						y: extent[1]
					};
					let pointmax = {
						x: extent[2],
						y: extent[3]
					};
					
					let urlq =
						self.$config.eviaSearchSource+"?lat1=" +
						pointmin.y +
						"&lng1=" +
						pointmin.x +
						"&lat2=" +
						pointmax.y +
						"&lng2=" +
						pointmax.x +
						"&years=2009&ignore=id,wkt";
					$.getJSON(urlq, function(data) {
						let json = data.info;
						let features = [];
						for(let i = 0; i < json.length; i++) {
							if(self.Json.findIndex((element) => (element.gid == json[i].gid)) > -1) {
								let index = self.Json.findIndex((element) => (element.gid == json[i].gid));
								if(index !== -1) {
									self.Json.splice(index, 1);
								}
							}
							let data = JSON.parse(json[i].geojson);
							let feature = {
								type: "Feature",
								geometry: data,
								id: json[i].gid
							};
							//移除选中
							if(self.features.findIndex((element) => (element.id == feature.id)) > -1) {
								let index = self.features.findIndex((element) => (element.id == feature.id));
								if(index !== -1) {
									self.features.splice(index, 1);
								}
							}
						}

						let geojsonObject = {
							type: "FeatureCollection",
							crs: {
								type: "name",
								properties: {
									name: "EPSG:4326"
								}
							},
							features: self.features
						};
						let vectorSource = new ol.source.Vector({
							features: new ol.format.GeoJSON({
								// featureProjection: "EPSG:3857"
							}).readFeatures(geojsonObject)
						});
						let vectorLayer = new ol.layer.Vector({
							//初始化矢量图层
							source: vectorSource,
							style: new ol.style.Style({
								fill: new ol.style.Fill({
									color: self.layerColor.draw.fillColor,
								}),
								stroke: new ol.style.Stroke({
									color: self.layerColor.draw.borderColor,
									width: 2
								})
							})
						});

						self.cleanMap("box");
						self.boxLayer = vectorLayer;
						self.map.addLayer(self.boxLayer);

					});
				});
				this.dragBox = dragBox;
			},
			handleDraw(command){
				switch(command){
					case "drawWFS" :
					this.drawWFS();
					break;
					case "cleanWFS" :
					this.cleanMap('draw');
					break;
					case "saveWFS" :
					this.saveWFS();
					break;

				}
			},
			drawWFS() { //手动绘制小班
				//清除之前的 drawInteraction
				this.cleanMap("draw");

				// 创建用于新绘制feature的layer
				let drawLayerA = new ol.layer.Vector({
					source: new ol.source.Vector(),
					style: new ol.style.Style({
						stroke: new ol.style.Stroke({
							color: 'blue',
							width: 5
						})
					})
				});

				// 添加绘制新图形的interaction，用于添加新的线条
				let drawInteractionA = new ol.interaction.Draw({
					type: 'Polygon',
					style: new ol.style.Style({
						stroke: new ol.style.Stroke({
							color: 'red',
							width: 10
						})
					}),
					source: drawLayerA.getSource()
				});

				let drawedFeatureA = [];

				drawInteractionA.on('drawend', function(e) {
					// 绘制结束时暂存绘制的feature
					drawedFeatureA.push(e.feature);
				});

				this.map.addLayer(drawLayerA);
				this.map.addInteraction(drawInteractionA);

				this.drawLayer = drawLayerA;
				this.drawedFeature = drawedFeatureA;
				this.drawInteraction = drawInteractionA;
			},
			saveWFS() { //保存到数据库

				if(this.drawedFeature.length > 0) {

					let newFeatures = []
					for(let i of this.drawedFeature) {

						// 转换坐标
						let geometry = i.getGeometry().clone();
						geometry = this.mapHelper.transForm(geometry,undefined,"EPSG:4326")

						geometry.applyTransform(function(flatCoordinates, flatCoordinates2, stride) {
							for(let j = 0; j < flatCoordinates.length; j += stride) {
								const y = flatCoordinates[j];
								const x = flatCoordinates[j + 1];
								flatCoordinates[j] = x;
								flatCoordinates[j + 1] = y;
							}
						});

						// 设置feature对应的属性，这些属性是根据数据源的字段来设置的
						let newFeature = new ol.Feature();
						newFeature.set('XMBH', this.xmbh);
						newFeature.set('State', 2); //指定只有造林设计才能添加  小班 且状态为未提交
						newFeature.setGeometryName('Shape'); //为geojson  geometry_name  重要***
						newFeature.setGeometry(new ol.geom.MultiPolygon([geometry.getCoordinates()]));

						newFeatures.push(newFeature);
					}

					this.addWFSToService(newFeatures);

				} else {
					this.$message.warning("您还未绘制图形，快去绘制吧。");
				}
			},
			addWFSToService(features) { // 把绘制的图层添加到服务器端  drawLaer
				let WFSTSerializer = new ol.format.WFS();
				let featObject = WFSTSerializer.writeTransaction(features,
					null, null, {
						featureType: 'YZL_ZLXB_PY', //layer name
						featureNS: 'smartForest', //工作区URI
						srsName: 'EPSG:4326' //坐标系
					});
				let serializer = new XMLSerializer();
				let featString = serializer.serializeToString(featObject);
				let request = new XMLHttpRequest();
				request.open('POST', 'http://39.105.11.71:8081/geoserver/smartForest/ows?service=WFS');
				request.setRequestHeader('Content-Type', 'text/xml');
				request.send(featString);

				let _this = this;
				request.onreadystatechange = function() {
					if(request.readyState == 4 && request.status == 200) {

						let match = request.responseText.match(/<wfs:totalInserted>(\S*)<\/wfs:totalInserted>/);
						let num = parseInt(match[1]) || 0;

						_this.addNum = num;
						_this.getXiaoBanList(true).then(() => {
							_this.getXiaoBanLayers();
						});

						_this.$message.success(`已成功添加${num}个小班信息`);
					} else {
						_this.addNum = 0;
						_this.$message.error(`添加失败,错误代码:${request.readyState}`);
					}
				}

			},
