							}
						})
						.catch(error => {
							this.ShuZhongPriceData = [];
							this.$message.error(error);
						})
				}else{
					this.ShuZhongPriceData = [];
					this.$message.error("没有标准ID");
				}
			},
			changeEcoic(val) { //选择改变经济模式，，标准
				console.log(val);
				for(let obj of this.EcoicIndex) {
					if(obj.id == val) {
						//	this.XiaoBanData.gclx = obj.gC_Id;
						/*this.XiaoBanData.zlfy = obj.zaoLin;
						this.XiaoBanData.zdfy = obj.zhengDi;
						this.XiaoBanData.fyfy = obj.fuYu;
						this.XiaoBanData.bzfy = obj.buZhi;
						this.XiaoBanData.tzde = obj.touZiDingE;
						this.XiaoBanData.ygdj = obj.yongGongDanJia;
						this.XiaoBanData.zysz = obj.miaoMuYiDanJia;
						this.XiaoBanData.bysz1 = obj.miaoMuErDanJia;
						this.XiaoBanData.bysz2 = obj.miaoMuSanDanJia;
						this.XiaoBanData.glf = obj.guanLiFei;
						this.XiaoBanData.ghf = obj.guanHuFei;
						this.XiaoBanData.kjtgf = obj.keJiTuiGuangFei;
						this.XiaoBanData.qtfy = obj.qiTaFeiYong;*/
						for(let ele in this.relationData.fromEcoicIndex) {
							this.XiaoBanData[ele] = obj[this.relationData.fromEcoicIndex[ele]]
						}
						
						//得到对应的树价目表
						this.getShuZhongPriceData(val);
						break;
					}
				}
			},
			setCoast() { //计算造林造价

				this.$refs["CostForm"].validate((valid) => {
					if(valid) {

					//	this.XiaoBanData.sheJiCoast = this.XiaoBanData.zdfy + this.XiaoBanData.zlfy + this.XiaoBanData.fyfy + this.XiaoBanData.bzfy;
						let guoJiaCoast = parseFloat(this.XiaoBanData.tzde) * parseFloat(this.XiaoBanData.zlmj);
					
						
						let treesPrice=0;
						let othersPrice = 0;
						//将可能是string的值装换成number
						let othersToNumArr = common.dataToNumber([this.XiaoBanData.zdfy,
															 this.XiaoBanData.zlfy,
															 this.XiaoBanData.fyfy,
															 this.XiaoBanData.bzfy,
															 
															 this.XiaoBanData.ygdj,
															 this.XiaoBanData.ghf,
															 this.XiaoBanData.kjtgf,
															 this.XiaoBanData.glf,
															 this.XiaoBanData.qtfy,
															])
						let zlmj = common.dataToNumber(this.XiaoBanData.zlmj);
						
						let total = 0;
						for(let n of othersToNumArr){
							total += n;
						}
						othersPrice = total * zlmj;
						
						if(this.ShuZhongPriceData.length>0){
							for(let obj of this.ShuZhongPriceData){
								
								shuzhongArr:
								for(let ele of this.ShuZhongData){
									
									if(obj.code == ele.shuzhong){
										
										treesPrice += (parseFloat(ele.czmd) * parseFloat(ele.hjbl)/100)*zlmj*(parseFloat(obj.danjia))
										
										break shuzhongArr;
									}
								}
								
							}
						console.log("treesPrice:"+treesPrice);
						console.log("othersPrice:"+othersPrice);
						let sheJiCoast = parseFloat( treesPrice +	othersPrice).toFixed(2) ;
						
						// 千分位	
						this.XiaoBanCoast.sheJiCoast = common.formatNumber(sheJiCoast,2,",");
						// 千分位	
						this.XiaoBanCoast.guoJiaCoast = common.formatNumber(guoJiaCoast,2,",");
						
						}else{
							this.$message.error("没有树种单价");
						}
						
					} else {
						this.$message.error("请选择经济标准");
					}
				})
			},

			submitJson(){ //提交框选的资源小班
				console.log(JSON.stringify(this.Json));
				if(this.Json.length > 0) {
					this.$confirm('是否确认信息并提交?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => {
						this.$axios.post(
							this.$config.serverUrl + "api/YZL_ZLXB_PY/SaveAddData",
							JSON.stringify(this.Json), {
								headers: {
									"Content-Type": "application/json"
								}
							}
						).then(res => {
							if(res.data == "新增成功") {
								this.Json = [];
								this.addNum = this.Json.length;
								this.getXiaoBanList(true);
								this.getXiaoBanLayers();
								this.$message.success(res.data);
							} else {
								this.$message.error(res.data);
							}
							
						}).catch(error => {
							this.$message.error(error);
						});
					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消'
						});
					});

				} else {
					this.$message.warning("您还没有选中小班数据，快去选择吧。");
				}

			},
			xiaoBanDelete(data, index) { //删除单个小班
				if(data.gid) { //删除Json 自己框选的数据

					this.$confirm('是否删除该小班？', '提示', { //自己的临时图层
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => {
						console.log("success");
						this.listData.splice(index, 1);
						this.Json.splice(index, 1);
						this.deleteAllXB = false;
						console.log(this.Json);
						//地图上消失
						this.delDataForMap(this.Json);
						this.$message.success("删除成功");

					}).catch((error) => {
						console.log(error);
						if(error == "cancel") {
							this.$message({
								type: 'info',
								message: "已取消"
							});
						} else {
							this.$message({
								type: 'info',
								message: error
							});
						}

					});

				} else if(data.id) { //删除service 数据     服务端获取的数据

					this.$confirm('此操作将永久删除该项目小班, 是否继续?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => {

						const id = data.id;
						this.$axios.delete(this.$config.serverUrl + "api/YZL_ZLXB_PY/DeleteYZL_ZLXB_PY/" + id,
							null, {
								headers: {
									'Content-Type': 'application/json'
								}
							}
						).then((res) => {

							this.$message.success("操作成功");
							this.cleanMap("service");
							this.cleanMap("active");

							this.addNum = 0;
							this.getXiaoBanList(true);
							this.getXiaoBanLayers(true);
						}).catch(error => {

							this.$message.error(error);
						})

					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消'
						});
					});
				} else {
					this.$message.error("数据错误");
				}
			},
			delListData() { //多个删除小班，，，多选  和全选
				if(this.multipleSelectionXB.length > 0) {

					if(this.multipleSelectionXB.length >= (this.Json.length + this.serviceData.length) && this.deleteAllXB) { //是否全选

						this.$confirm('是否删除该项目下的所有小班数据？', '提示', {
							confirmButtonText: '确定',
							cancelButtonText: '取消',
							type: 'warning'
						}).then(() => {

							this.$axios.delete(this.$config.serverUrl + "api/YZL_ZLXB_PY/DeleteYZL_ZLXB_PYsByXmbh/" + this.xmbh,
								null, {
									headers: {
										'Content-Type': 'application/json'
									}
								}
							).then((res) => {
								this.Json = [];
								this.serviceData = [];
								this.listData = [];
								this.getXiaoBanLayers();
								this.$message.success("操作成功");
							}).catch(error => {

								this.$message.error(error);
							})

						}).catch(() => {
							this.$refs.multipleTable.clearSelection();
							this.deleteAllXB = false;
							this.$message({
								type: 'info',
								message: '已取消'
							});
						})

					} else { //手动多选的

						this.$confirm('此操作将永久删除所选中信息, 是否继续?', '提示', {
							confirmButtonText: '确定',
							cancelButtonText: '取消',
							type: 'warning'
						}).then(() => {

							let Jdata = this.Json;
							let arrDel = [];
							for(let obj of this.multipleSelectionXB) {

								if(obj.gid) { //有gid 是自己框选的数据--Json

									for(let i = 0; i < Jdata.length; i++) {
										if(Jdata[i].gid == obj.gid) {

											Jdata.splice(i, 1);

											break;
										}
									}

								} else if(obj.id) { //有id 是数据库的数据 -- serviceData

									for(let ele of this.serviceData) {
										if(ele.id == obj.id) {
											arrDel.push(obj.id);
										}
									}
								}

							}
							this.Json = Jdata;
							this.delDataForMap(Jdata);
							this.$axios.post(this.$config.serverUrl + "api/YZL_ZLXB_PY/DeleteYZL_ZLXB_PYs/",
								JSON.stringify(arrDel), {
									headers: {
										'Content-Type': 'application/json'
									}
								}
							).then((res) => {

								this.getXiaoBanList(true);
								this.cleanMap("service");
								this.getXiaoBanLayers(true); //保留临时画的
								this.deleteAllXB = false;
								this.$message.success("操作成功");
							}).catch(error => {

								this.$message.error(error);
							})

						}).catch(() => {
							this.$refs.multipleTable.clearSelection();
							this.$message({
								type: 'info',
								message: '已取消'
							});
						});
					}

				} else {
					this.$message.warning("您未选中选中任何数据。");
				}
			},
			deleteAll() { //全部删除，，删除所有，，包括没有看见的
				this.deleteAllXB = true;
			},

			allotDialog() { //分发小班的人员和机构树
				if(this.multipleSelectionXB.length > 0) {

					this.$axios.post(this.$config.serverUrl + "api/YZL_ZLXB_PY/GetOraPersonInfo",
						JSON.stringify(sessionStorage.getItem("token")), {
							headers: {
								'Content-Type': 'application/json'
							}
						}
					).then((res) => {
						console.log(res);
						if(res.data) {
							this.allot = true;
							let treeJson = res.data;

							let render = function(treeJson) {

								if(!Array.isArray(treeJson) && treeJson.length <= 0) {
									return;
								} else {

									let Data = [];
									for(let i = 0; i < treeJson.length; i++) {

										let objO = {
											name: treeJson[i].name,
											id: treeJson[i].createDate,
											children: [],
											type: "org",
										}
										objO.children = render(treeJson[i].children);
										if(Array.isArray(treeJson[i].users) && treeJson[i].users.length > 0) {

											for(let c = 0; c < treeJson[i].users.length; c++) {

												let objU = {
													name: treeJson[i].users[c].userName,
													id: treeJson[i].users[c].id,
													type: "person",
												}
												objO.children.unshift(objU);
											}
										}

										Data.push(objO);
									}

									return Data;
								}
							}
							console.log(this.allotData);
							this.allotData = render([treeJson]);

						} else {
							this.$message.error("数据错误");
						}

					}).catch(error => {

						this.$message.error(error);
					})

				} else {

					this.$message.warning("您未选中选中任何数据。");
				}

			},
			allotCancel() { //取消分发
				this.allot = false;
				this.checkedPerson = {};
			},
			allotXiaoBan() { //分发小班给个人
				if(this.checkedPerson.id) {

					console.log(this.multipleSelectionXB);
					let allotArr = [];
					for(let ele of this.multipleSelectionXB) {
						let obj = {
							id: ele.id,
							userId: this.checkedPerson.id
						}
						allotArr.push(obj);
					}

					this.$axios.post(this.$config.serverUrl + "api/YZL_ZLXB_PY/UpdateLockUserId",
						JSON.stringify(allotArr), {
							headers: {
								'Content-Type': 'application/json'
							}
						}
					).then((res) => {
						this.allot = false;
						this.$refs.multipleTable.clearSelection();
						this.$message.success("操作成功");
					}).catch(error => {

						this.$message.error(error);
					})

				} else {
					this.$message.warning("请选择人员。");
				}
			}

		}
	};
</script>
<style>
	@import "../common/css/proCommon.css";
	div#workMap {
		margin: -10px;
	}
	
	#workMap {
		height: calc(100% - 30px);
		width: 100%;
		position: absolute;
		z-index: 10;
	}
	
	div.control {
		position: absolute;
		z-index: 20;
	}
	
	.dialogDragModal .el-dialog__wrapper {
		position: inherit !important;
	}
	
	.dialogDragModal .el-dialog {
		position: absolute;
		margin: 0;
		z-index: 21;
		width: 620px;
		top: -7vh;
		right: 2vh;
	}
	.dialogDragModal .drageDialog .el-dialog {
		height: calc(100% - 126px);
	}
