	}
	
	.dialogDragModal .el-dialog__body .tabs{
		height:100%;
	}
	.dialogDragModal .el-dialog__body .ivu-tabs{
		height:100%;
	}
	.dialogDragModal .el-dialog__body .ivu-tabs .ivu-tabs-content-animated{
		height: 100%;
	}
	.dialogDragModal .el-dialog__body .ivu-tabs .ivu-tabs-tabpane{
		height: calc(100% - 52px);
    	overflow-y: auto;
    	overflow-x: hidden;
	}
	.dialogDragModal .el-dialog__body .ivu-tabs .ivu-tabs-tabpane .el-form--inline .el-form-item{
		margin-right: 0;
	}
	/************禁止'小班编辑'信息选中***************************/
	
	.dialogDragModal .el-dialog__header .el-dialog__title,div#mouse-position{
		-webkit-touch-callout: none;
		/* iOS Safari */
		-webkit-user-select: none;
		/* Chrome/Safari/Opera */
		-khtml-user-select: none;
		/* Konqueror */
		-moz-user-select: none;
		/* Firefox */
		-ms-user-select: none;
		/* Internet Explorer/Edge */
		user-select: none;
		/* Non-prefixed version, currently not supported by any browser */
	}
	div.selectBox div.el-dialog{
		margin-top:0;
		width:210px;
		left:400px;
		top:-10vh;
	}
	.scroll {
		position: absolute;
		padding-left: 10px;
		padding-right: 0;
		z-index: 22;
		width: 374px;
		left: 10px;
		top: 50px;
		background: #fff;
	}
	
	.s-header {
		position: fixed;
		z-index: 4;
		width: 344px;
		height: 50px;
		top: 120px;
		background: #fff;
		padding-top: 10px;
	}
	
	.s-header>div {
		width: 162px;
		margin-right: 4px;
		float: left;
	}
	.scrollList{
		height:500px;
		overflow-y:auto;
		position: absolute;
		z-index: 22;
		width: 620px;
		top: -7vh;
		right: 5vh;
	}
	div.tips {
		margin-top: 8px;
		font-size: 14px;
	}
	
	.blueSpan {
		color: #409EFF;
		font-size: 14px;
		margin: 10px 5px 0 5px;
	}
	
	.tooHeight {
		height: 27px;
	}
	
	
	.s-header .el-button+.el-button {
		margin-left: 0;
	}
	
	.s-body {
		position: relative;
		top: 40px;
	}
	/***************固定表头的表格 样式调整*************/
/*	.s-body .el-table__header-wrapper{
		    position: fixed;

    z-index: 8;
	}*/
	/************dialog Form input******************/
	
	.dialogDragModal .tabs .el-form-item__content {
		width: 182px;
	}
	.dialogDragModal div.ShuInputS {
		position: absolute;
		margin-left:4px;
	}
	.dialogDragModal span.ShuLabel {
		display: inline-block;
		text-align: right;
		width: 76px;
		padding-right: 16px;
	    padding-bottom: 10px;
	    padding-top: 4px;
	}
	
	.dialogDragModal .ShuRow .el-input {
		width: 174px;
	}
	/*******走马灯样式调整***************/
	
	div.carousel {
		margin-bottom: 20px;
	}
	
	.el-carousel__item h3 {
		color: #475669;
		font-size: 14px;
		opacity: 0.75;
		line-height: 150px;
