	}
	/*文字居中*/
	
	.el-carousel__item {
		text-align: center;
	}
	
	.el-dialog__footer {
		text-align: left;
	}
	/*表格不同的颜色*/
	
	.el-table .warning-row {
		background: oldlace;
	}
	
	.el-table .green-row {
		background: #f0f9eb;
