	}
	span.linkActive{
		color:#409EFF;
		cursor: pointer;
	}
	span.linkActive:hover{
		text-decoration:underline;
	}
	span.linkDisabled,span.disabled{
		color:#C0C4CC;
		font-style: italic;
		cursor: not-allowed;
	}

	/* fullRow */
	.fullRow .el-select{
		width:100%;
	}
	.dialogDragModal .tabs .fullRow .el-form-item__content{
		width:464px;
	}
</style>


