function SetTableID(layer, tbID, guess = true) {

    if (!layer) {
        return
    }

    if (tbID) {
        layer.set(specialField.translateTable, tbID)
    }

    if (!guess) {
        return
    }

    var urlStr = layer.getSource().getUrl();
    if (!urlStr) {
        return
    }
    var url = new URL(urlStr);
    var tbID = url.searchParams.get(specialField.translateTable);
    if (!tbID) {
        tbID = url.searchParams.get('typeName')
    }

    if (tbID) {
        layer.set(specialField.translateTable, tbID)
    }

    //TODO:取出url里的参数




}


