<template>
	
	
    <div class="table">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-lx-cascades"></i>{{this.$route.meta.title}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
        	<div class="table">
        		 <div class="handle-box">
	           <!--     <el-button  type="primary" icon="el-icon-delete" class="handle-del mr10" @click="delAll">批量删除</el-button>-->
	                <el-input v-model="keyword" placeholder="搜索关键词" class="handle-input mr10"></el-input>
	                <el-button type="primary" icon="el-icon-search" @click="getData">搜索</el-button>
	                <el-button class="rightRefresh" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
	            </div>
	            <el-table 
	            	v-loading="loading"
	            	:data="tableData" 
	            	highlight-current-row
	            	border 
	            	class="table" 
	            	ref="multipleTable" 
	            	@selection-change="handleSelectionChange">
	                <el-table-column prop="createDate" label="日期" 
	                	:formatter="formatter"
	                	 align="center" width="160">
	                </el-table-column>
	                <el-table-column prop="xmbh" label="项目编号" align="center" width="130">
	                </el-table-column>
	                <el-table-column prop="xmmc" label="项目名称"  align="center" >
	                </el-table-column>
	                <el-table-column
				      prop="state_Name"
				      align="center"
				      label="状态"
				      width="120"
				>
				      <template slot-scope="scope">
				        <Tag
				          :color="getState(scope.row.state_Name)"
				          type="dot"
				          fade>{{scope.row.state_Name}}</Tag>
				      </template>
				    </el-table-column>
	               <!--本页表格数据筛选
	               	<el-table-column
				      prop="state"
				      align="center"
				      label="进度"
				      width="100"
				      :filters="[{ text: '待审核', value: '待审核' }, { text: '待提交', value: '待提交' }]"
				      :filter-method="filterTag"
				      filter-placement="bottom-end">
				      <template slot-scope="scope">
				        <el-tag
				          :type="scope.row.state === '待审核' ? 'primary' : 'success'"
				          disable-transitions>{{scope.row.state}}</el-tag>
				      </template>
				    </el-table-column>-->
	                <el-table-column label="小班数" width="180" align="center">
	                    <template slot-scope="scope">

						    <el-tooltip 
	                        	class="item" 
	                        	effect="dark" 
	                        	placement="top"
	                        	>
	                        	<div slot="content" v-if="scope.row.state_Name =='审核中'">{{scope.row.currentStatesCount || 0}}个小班已审核，总共{{scope.row.totalCount || 0}}个小班</div>
						       <el-button
						       	:disabled="scope.row.state_Name =='审核中' ? null :'disabled' "  
						       	 @click="toSubcompartment(scope.row,'watch')">{{scope.row.currentStatesCount || 0}}/{{scope.row.totalCount || 0}}</el-button>
						    </el-tooltip>
						    
						    
	                    </template>
	                </el-table-column>
	                <el-table-column label="操作" align="center" width="300">
	                    <template slot-scope="scope">
	                        <el-button  :disabled="scope.row.state_Name =='审核中' ? null :'disabled' "  
	                        	type="text" 
	                        	icon="el-icon-edit" 
	                        	@click="handleEdit(scope.$index, scope.row)">审核</el-button>
	                        
	                        <el-button 
	                        	type="text" 
	                        	icon="fa fa-reply" 
	                        	v-show="check" 
	                        	:disabled="scope.row.state_Name =='审核中' ? null :'disabled' "  
	                        	:class="scope.row.state_Name =='审核中' ? 'red' : null " 
	                        	@click="toNext(scope.row,'unpass')">&nbsp;退回</el-button>
	                        <el-button 
	                        	type="text" 
	                        	:icon="scope.row.state_Name =='审核中'? 'fa fa-calendar-check-o' : 'fa fa-hand-o-right'" 
	                        	v-show="check" 
	                        	:disabled="!check" 
	                        	@click="toNext(scope.row,'pass')">&nbsp;{{scope.row.state_Name =='审核中'? "通过" : "验收"}}</el-button>
	                    </template>
	                </el-table-column>
	            </el-table>
	            <div class="pagination">
					 <el-pagination
				      @size-change="handleSizeChange"
				      @current-change="handleCurrentChange"
				      :current-page="currentPage"
				      :page-sizes="[5, 10, 15, 20]"
				      :page-size="pageSize"
				      layout="total, sizes, prev, pager, next, jumper"
				      :total="totalPage">
				    </el-pagination>
	            </div>
        	</div>
        </div>
		

		<!--抽屉框，查看-->
		<Drawer
            title="项目信息"
            v-model="drawer"
            width="720"
            :mask-closable="false"
            :styles="styles"
        >
	    	<div id="approval">
	    		<div class="table" >
		        	<Tabs  v-model="activeName">
					    <TabPane  label="项目基本信息" name="first">
					    	 <el-form 
					    	 	class = "formDiv"
					    	 	ref="Form"
					    	 	size = "small"
					    	 	:rules="formDatatValidate" 
					    	 	:model="formData">
				                <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="项目名称:" label-position="top" class="noMargin">
				                           <!-- <el-input disabled v-model="formData.name" placeholder="please enter user name" />-->
				                            <el-input disabled v-model="formData.xmmc"  ></el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目编号:" label-position="top" class="noMargin" >
				                            <el-input disabled v-model="formData.xmbh"  >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="项目类别:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.xmlb"  >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="工程实施年度:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.gcssnd"  >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                 <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="投资文件文号:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.tzwjwh"  >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目总投资:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.xmztz" >
				                            	<template slot="append">万元</template>
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                <el-row :gutter="32">
				                	<el-col :span="12">
				                        <el-form-item label="工程类别:" label-position="top" class="noMargin">
				                            <el-select 
				                            	disabled
				                            	size = "small"
				                            	v-model="formData.gclb" 
				                            	class = "selectDiv">
				                            </el-select>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="营造林方式 :" label-position="top" class="noMargin">
				                            <el-select 
				                            	disabled
				                            	size = "small"
				                            	v-model="formData.yzlfs" 
				                            	class = "selectDiv">
				                            </el-select>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="建设内容:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.jsnr" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="建设规模:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.jsgm" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                       <el-form-item label="项目实施主体:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.xmzt" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目负责人:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.xmfzr" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                 </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                       <el-form-item label="设计单位:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.xmsjdw" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="设计日期:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.xmsjrq">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    
				                 </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                        <el-form-item label="设计证书等级:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.SJZSDJ" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                 	<el-col :span="12">
				                        <el-form-item label="设计单位负责人:" label-position="top" class="noMargin">
				                            <el-input disabled v-model="formData.SJDWFZR" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    
				                 </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                        <el-form-item label="描述" label-position="top" class="noMargin">
						                    <el-input disabled type="textarea" v-model="formData.bz" :rows="4" />
						                </el-form-item>
				                    </el-col>
				                 	<el-col :span="12">       
						                <el-form-item label="审批意见" label-position="top" class="noMargin" prop="spyj">
						                    <el-input type="textarea" v-model="formData.spyj" :rows="4" placeholder="请填写" />
						                </el-form-item>
				                    </el-col>
				                    
				                 </el-row>
				                
				                <el-row :gutter="24">
				                	<el-col :span="24">
				                		<div style="margin-top:16px">
				                			<Button  type="primary" @click="submitFrom('Form')">保存 </Button >
				                		</div>
				                	</el-col>
				                </el-row>
				            </el-form>
					    </TabPane>
					    <TabPane label="项目材料" name="second">
					    	<upload-file></upload-file>
					    	
					    </TabPane>
					    <TabPane label="造林小班" name="third">
					    	<xiaoban-list v-bind:XMBH="formData.xmbh" v-bind:state="formData.state" :drawer.sync="drawer" ></xiaoban-list>
					    </TabPane>
					    <TabPane label="项目经费" name="fourth">项目经费</TabPane>
					 </Tabs >
					 <span>版本号:<span class="blueText">{{formData.version}}</span>；
					 	<span class="blueText">{{formData.createUser_Name}}</span>于
					 	{{formData.createDate }}&nbsp;提交，
					 	<span class="blueText">{{formData.updateUser_Name}}</span>{{ formData.updateDate }}&nbsp;审批</span>
					 <div class="demo-drawer-footer">
				        
				        <Button type="primary" @click="toNext(null,'pass')">通过</Button>
				        <Button style="margin-left: 8px" type="error" @click="toNext(null,'unpass')">退回</Button>
				        <Button style="margin-left: 8px" @click="drawer = false">关闭</Button>
				      </div>
	        	</div>
	        	
	    		
	    		
	    	</div>
       </Drawer>    

    </div>
</template>

<script>
	import common from "../common/js/common.js"
	import xiaobanList from "../common/vue/XiaoBanList.vue"
	import uploadFile from "../common/vue/Upload"
    export default {
        name: 'basetable',
        components:{
           uploadFile:uploadFile,
           xiaobanList,
        },
        data() {
            return {
                tableData: [],
                loading:true,
                currentPage: 1,
                pageSize:5,
                totalPage:0,
                activeName:"first",
                multipleSelection: [],
                selectProState: [],
                check:true,
                drawer:false,
                titleMsg:"",
                styles: {
                    height: 'calc(100% - 55px)',
                    overflow: 'auto',
                    fontSize:"16px",
                    paddingBottom: '53px',
                    position: 'static'
                },
                formData: {
                    xmmc: '',
                    xmbh:"",
                    xmlb: '',
                    state:"",
                    gclb: '',
                    yzlfs: '',
                    gcssnd:"",
                    tzwjwh:"",
                    xmztz:"",
                    jsnr:"",
                    jsgm:"",
                    xmzt: '',
                    xmfzr:'',
                    xmsjdw: '',
                    xmsjrq:'',
                    bz: '',
                    ysyj:"",
                },
                keyword: '',
                rowId:"",
                idx: -1,  //选中页的行数
                formDatatValidate:{
                	spyj: [
                        { required: true, message: '请输入项目审批意见', trigger: 'blur' }
                    ],
                }
            }
        },
        created() {
            this.getData();
        },
        computed: {
        	 turnToApproval() {
        	 	if(this.$store.state.pro.name && this.$route.params.ticket=="ForestApproval"){
        	 		this.keyword = this.$store.state.pro.name;
        	 		this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/GetYZL_ZLXM", 
	                JSON.stringify({
	                	    keyword:this.keyword ,
	                	    states:[3,4],
	                	    token:sessionStorage.getItem("token"),
	                	    isSubcompartmentInfo: true,
		                	pageQueryInfo:{
				                	pageSize: this.pageSize,
				    				pageIndex: this.currentPage
		                	},
	                	}),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	                	console.log(res);
	                    this.tableData = res.data.resultValue;
	                    this.totalPage=res.data.count;
	                    this.loading= false;
	                    this.$refs.multipleTable.setCurrentRow(this.tableData[0]);
    
	                }).catch(error=>{
	                	this.$message.error(error);
	                	})
        	 	}
			    return this.$store.state.pro;
			  },
        },
        watch:{
				 turnToApproval(val) {
				     return this.$store.state.pro;
				},
		},
        methods: {
        	getState(state){
        		let str ="";
        		switch(state){
        			
        			case "审核中":
        			str =   "primary";
        			break;
        			break;
        			case "待验收":
        			str =   "#87e8de";
        			break;
        			case "待核查":
        			str =   "success";
        			break;
        			case "已退回":
        			str =   "error";
        			break;
        			default:
        			str =   "primary";
        		}
        		return str;
        	},
        	handleSizeChange(val){
        		this.pageSize = val ;
        		this.getData();
        	},
            // 分页导航
            handleCurrentChange(val) {	
                this.currentPage = val;
                this.getData();
            },
            refresh(){  //重置
            	this.$store.commit("setPro",{});
            	this.keyword = "";
            	this.currentPage= 1;
                this.pageSize=5;
                this.getData();
            },
            // 获取 easy-mock 的模拟数据
            getData() {
                
              return   this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/GetYZL_ZLXM", 
                JSON.stringify({
                	    keyword:$.trim(this.keyword),
                	    states:[3,4],
                	    isSubcompartmentInfo: true,
                	    token:sessionStorage.getItem("token"),
	                	pageQueryInfo:{
			                	pageSize: this.pageSize,
			    				pageIndex: this.currentPage
	                	},
                	}),
                	{headers: {'Content-Type': 'application/json'}}
                ).then((res) => {
                	console.log(res);
                    this.tableData = res.data.resultValue;
                    this.totalPage=res.data.count;
                    this.loading= false;
                    
                           
                }).catch(error=>{
                	this.$message.error(error);
                	})
            },
            toNext(row,to){
            	if(row && row.state_Name){  //toRow
            		
            		if(row.state_Name == "审核中"){  //审核操作
            			this.formData = row;
		            	if(to == "pass"){ //通过
		            		this.formData.state = 4;
		            		this.titleMsg = "是否确认项目信息并审批通过？"
		            	
		            		
		            	}else if(to == "unpass"){  //退回
		            		this.formData.state = 1;
		            		this.titleMsg = "是否确认项目信息经审批不通过，申请被退回？"
		            	}
		            	this.$confirm(this.titleMsg, '提示', {
					          confirmButtonText: '确定',
					          cancelButtonText: '取消',
					          type: 'warning'
					        }).then(() => {
					           
					           this.submitFrom('submit');
					           
					        }).catch(() => {
					             this.$message.info("已取消");        
					        });
	            	}else if(row.state_Name == "待验收"){  //去验收操作
	            		
	            		//	this.$router.replace("/icon");
	            		this.$store.commit("setPro",{
	            					proID:row.id,
	            					name:row.xmmc,
	            			});
	            		this.$router.push({name:'ForestAccept',params:{
	            									proID:row.id,
	            									name:row.xmmc,
	            									ticket:'ForestAccept',
	            									}});
	            		
	            	}
	            	
            	}else{   //toFormData 
            		
		            if(to){
		            	
		            	if(to == "pass"){ //通过
		            		this.formData.state = 4;
		            		this.titleMsg = "是否确认项目信息并审批通过？";
			            }else if(to == "unpass"){  //退回
			            	this.formData.state = 1;
			            	this.titleMsg = "是否确认项目信息经审批不通过，申请被退回？";	
			            }
		            	
		            	this.$confirm(this.titleMsg, '提示', {
					          confirmButtonText: '确定',
					          cancelButtonText: '取消',
					          type: 'warning'
					        }).then(() => {
					           
					           this.submitFrom('submit');
					           
					        }).catch(() => {
					             this.$message.info("已取消");        
					        });
		            }else{
		            	this.$message.error("未能到达，数据错误");
		            }
		            
		            
		            
            	}
            	
            	
            	
            },
            search() {
                getData();
            },
			formatter(row,ele){
 				return common.formatDate(row.createDate);
			},
            handleEdit(index, row) {
            	//深拷贝，，表单与table解绑，，，，主要是解决重置表单后，表格的数据跟着被清空
            	this.tableData[this.idx] = JSON.parse(JSON.stringify(this.formData));
            	
                this.idx = index;
                //重置表单
                this.$refs["Form"].resetFields();
                let  item = this.tableData[index];
                console.log(item);
                item.createDate = common.formatDate(item.createDate);
                item.updateDate = common.formatDate(item.updateDate);
                item.type = item.type + "";
                item.method = item.method + "";
                this.formData = item;
                this.drawer = true;
                
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            toSubcompartment(data,type){
            	
            	
            	this.drawer = false;
            	this.$store.commit("setXmbh",{
            		xmbh:data? data.xmbh : this.XMBH,
            		ToState:4,
            	});
            	this.$router.push({
            						name:"SubcompartmentWorkSpace",
            						params:{
            							type,
            							xmbh: data ? data.xmbh : this.XMBH,
            							ToState:4,
            							ToName:"审批后待验收"}
            						});
            	
            	
            
            },
            submitFrom(name){
            	
            	if(name == "Form"){  //保存，，，保存审批意见 但没有退回或者通过
            			this.$refs["Form"].validate((valid) => {
		                    if (valid) {
		                    	console.log( this.formData);
		                    	let value = this.formData;
			                    	value.token = this.$store.state.token;
			                		!value.id ? value.id =this.rowId:null;
		                    	this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/UpdateYZL_ZLXM", 
					                JSON.stringify(value),
					                	{headers: {'Content-Type': 'application/json'}}
					                ).then((res) => {
					                	getData();
					                	this.$message.success("保存成功");
					                }).catch(error=>{
					                
		                				this.$message.error(error);
		                			})
		                
		                    } else {
		                        this.$Message.error('请正确填写');
		                    }
		                })
            	}else if (name == "submit"){
            		
            		this.$refs["Form"].validate((valid) => {
		                    if (valid) {
		                    	
		                    	let value = this.formData;
			                    	value.token = this.$store.state.token;
			                		!value.id ? value.id =this.rowId:null;

		                    	this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/UpdateYZL_ZLXM", 
					                JSON.stringify(value),
					                	{headers: {'Content-Type': 'application/json'}}
					                ).then((res) => {
					                	
					                	this.$message.success("操作成功");
					                	this.drawer = false;
					                	this.getData();
					                }).catch(error=>{
					                
		                				this.$message.error(error);
		                			})
		                
		                    } else {
		                        this.$Message.error('请正确填写审批意见');
		                    }
		                })
            		
            		
            	}else{
            		this.$Message.error('数据错误');
            	}
            
            }
            
        }
    }

</script>

