<template>
  <div class="table">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-lx-cascades"></i> 基础表格</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
    	<el-row>
    		<el-col :span="24">
    			<div class="handle-box">
		        <el-autocomplete v-model="select_word" placeholder="筛选关键词" class="handle-input mr10" :fetch-suggestions="querySearchAsync" @select="search"></el-autocomplete>
		        <el-button type="primary" icon="search" @click="search">搜索</el-button>
		        <el-button type="primary" icon="search" @click="handleAdd()">新增</el-button>
		      </div>
    		</el-col>
    	</el-row>
    	<el-row :gutter="20">
    		<el-col :span="6">
    				<el-tree :data="dataprop" 
    					:props="props1" 
    					node-key="code" 
    					ref="tree" 
              default-expand-all
    					@node-click="handleNodeClick"></el-tree>
    		</el-col>
    		<el-col :span="18">
    			     <el-table :data="data" border 
    			     	class="tablemain" 
    			     	ref="multipleTable"
    			     	@selection-change="handleSelectionChange">
				          <el-table-column prop="xuhao" label="序号" width="80" align= "center"></el-table-column>
                  <el-table-column prop="jiHuaMingCheng" label="计划名称"  align= "center">
				          </el-table-column>
				          <el-table-column prop="sheng_Name" label="省"  align= "center">
				          </el-table-column>
				          <el-table-column prop="shi_Name" label="市"  align= "center">
				          </el-table-column>
				          <el-table-column prop="xian_Name" label="县"  align= "center">
				          </el-table-column>
                  <el-table-column prop="wenHao" label="文号"  align= "center">
				          </el-table-column>
                  <el-table-column prop="zhongYangTouZi" label="中央投资"  align= "center">
				          </el-table-column>
				          <el-table-column prop="diFangPeiZhi" label="地方配置"  align= "center">
				          </el-table-column>
                  <el-table-column prop="zqId" label="政区"  align= "center">
				          </el-table-column>
				          <el-table-column prop="zaoLinDate" label="造林年度"  align= "center">
				          </el-table-column>
				          <el-table-column prop="gongchengleibie_Name" label="工程类别" align= "center" >
				          </el-table-column>
				          <el-table-column prop="jiHuaMianJi" label="造林计划面积(亩)" align= "center">
				          </el-table-column>
				          <el-table-column label="操作" width="240" align="center"> 
				            <template slot-scope="scope">
				              <el-button type="text" icon="el-icon-edit" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
				              <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                      <el-button type="text" icon="el-icon-view" @click="handlecheck(scope.$index, scope.row)">查看工程</el-button>
				            </template>
				          </el-table-column>
				        </el-table>
				        <div class="formBottom">
				        	<el-form ref="form" :model="form" label-width="280px">
					          <el-form-item :label="zq_name+'造林计划面积统计(平方千米)'">
					            <el-input v-model="form.totle" :disabled="true"></el-input>
					          </el-form-item>
					        </el-form>
				        </div>

				         <div class="pagination">
					        <el-pagination background @current-change="handleCurrentChange" layout="total,prev, pager, next" :current-page.sync="currentPage1" :total="total">
					        </el-pagination>
					      </div>
    		</el-col>
    	</el-row>
    </div>
    <!-- 新增弹出框 -->
      <Drawer
            title="新增"
            v-model="dialogVisible"
            width="850"
            :mask-closable="false"
            :styles="styles"
        >
      <el-form ref="form" :model="form" label-width="110px">
        <el-form-item label="计划名称" prop="jiHuaMingCheng">
          <el-input v-model="form.jiHuaMingCheng" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="文号" prop="wenHao">
          <el-input v-model="form.wenHao" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="行政区域" prop="sheng">
          <el-select v-model="form.sheng" filterable placeholder="请选择" @change="currentJB">
            <el-option v-for="item in parentoptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
          <el-select v-model="form.shi" filterable placeholder="请选择" @change="currentJB">
            <el-option v-for="item in parentoptions_shi" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
          <el-select v-model="form.xian" filterable placeholder="请选择" @change="currentJB">
            <el-option v-for="item in parentoptions_xian" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="造林年度" prop="zaoLinDate">
          <el-input v-model="form.zaoLinDate" placeholder="请输入内容(年份)" ><template slot="append">年</template> </el-input>
        </el-form-item>
        <el-form-item label="工程类别" prop="gongChengLeiBie">
          <el-select v-model="form.gongChengLeiBie" filterable placeholder="请选择">
            <el-option v-for="item in gongchengoptions" :key="item.code" :label="item.name" :value="item.code">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="中央投资" prop="zhongYangTouZi">
          <el-input v-model="form.zhongYangTouZi" type="number" placeholder="请输入内容"><template slot="append">(元)</template> </el-input>
        </el-form-item>
        <el-form-item label="地方配置" prop="diFangPeiZhi">
          <el-input v-model="form.diFangPeiZhi" type="number" placeholder="请输入内容"><template slot="append">(元)</template> </el-input>
        </el-form-item>
        <el-form-item label="造林计划面积" prop="jiHuaMianJi">
          <el-input v-model="form.jiHuaMianJi" type="number" placeholder="请输入内容"><template slot="append">(亩)</template> </el-input>
        </el-form-item>
      </el-form>
       <div class="demo-drawer-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveAdd">确 定</el-button>
      </div>
    </Drawer> 
    <!-- 编辑弹出框 -->
       <Drawer
            title="编辑"
            v-model="editVisible"
            width="700"
            :mask-closable="false"
            :styles="styles"
        >
      <el-form ref="Form" :model="Form" label-width="120px">
        <el-form-item label="计划名称">
          <el-input v-model="Form.jiHuaMingCheng" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="文号">
          <el-input v-model="Form.wenHao" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="造林年度">
          <el-input v-model="Form.zaoLinDate"><template slot="append">年</template>  </el-input>
        </el-form-item>
        <el-form-item label="工程类别">
         <el-select v-model="Form.gongChengLeiBie" filterable placeholder="请选择">
            <el-option v-for="item in gongchengoptions" :key="item.code" :label="item.name" :value="item.code">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="中央投资">
          <el-input v-model="Form.zhongYangTouZi" type="number" placeholder="请输入内容"><template slot="append">(元)</template> </el-input>
        </el-form-item>
        <el-form-item label="地方配置">
          <el-input v-model="Form.diFangPeiZhi" type="number" placeholder="请输入内容"><template slot="append">(元)</template> </el-input>
        </el-form-item>
        <el-form-item label="造林计划面积">
          <el-input v-model="Form.jiHuaMianJi" type="number"><template slot="append">(亩)</template></el-input>
        </el-form-item>

      </el-form>
      <div class="demo-drawer-footer">
          <el-button @click="editVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveEdit">确 定</el-button>
      </div>
     </Drawer> 

    <!-- 删除提示框 -->
    <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
      <el-form ref="Form" :model="Form" label-width="50px">
      </el-form>
      <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delVisible = false">取 消</el-button>
        <el-button type="primary" @click="deleteRow()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "zaolinjihua",
  data() {
    return {
      Url: "./static/vuetable.json",
      tableData: [],
      alltableData: [],
      treeData: [],
      restaurants: [],
      zq_name: "",
      styles: {
        height: 'calc(100% - 55px)',
        overflow: 'auto',
        paddingBottom: '53px',
        position: 'static'
      },
      total: 1,
      value1: "",
      page_size: 10,
      multipleSelection: [],
      select_cate: "",
      select_word: "",
      del_list: [],
      is_search: false,
      editVisible: false,
      dialogVisible: false,
      delVisible: false,
      currentPage1: 1,
      Form: {
        jiHuaMingCheng: "",
        sheng: "",
        shi: "",
        xian: "",
        zaoLinDate: "",
        gongChengLeiBie: "",
        jiHuaMianJi: "",
        zhongYangTouZi: "",
        zqId: "",
        diFangPeiZhi: "",
        wenHao: "",
        id: "",
        Token: "",
        index: ""
      },
      form: {
        jiHuaMingCheng: "",
        sheng: "",
        shi: "",
        zqId: "",
        xian: "",
        totle: "",
        wenHao: "",
        zaoLinDate: "",
        gongChengLeiBie: "",
        zhongYangTouZi: "",
        diFangPeiZhi: "",
        jiHuaMianJi: "",
        Token: "",
        index: ""
      },
      gongchengoptions: [],
      parentoptions: [],
      parentoptions_shi: [],
      parentoptions_xian: [],
      dic: [],
      dic_shi: [],
      dic_xian: [],
      dic_xiang: [],
      dic_cun: [],
      selVal: "",
      idx: -1,
      dataprop: [],
      props1: {
        label: "name",
        children: "children",
        isLeaf: "leaf"
      }
    };
  },
  beforeCreate() {
    //this.loadNode1(node, resolve);
  },
  created() {
    var ZQcode = sessionStorage.getItem("zqCode").split(",");
    console.log(this.$store.state.loginInfo);
    this.GetShengDataByCode(ZQcode);
    this.getgongchenData();

  },
  mounted() {},
  computed: {
    data() {
      return this.tableData;
    }
  },
  methods: {
    // 分页导航
    handleCurrentChange(val) {
      var length = 10;
      var start = length * (val - 1);
      var end = start + 10;
      this.tableData = this.alltableData.slice(start, end);
    },
   createStateFilter(queryString) {
        return (state) => {
          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect(item) {
        console.log(item);
      },
    //
    querySearchAsync(queryString, callback) {
      var restaurants = this.restaurants;
      // 如果参数都没变化，则使用缓存数据，避免请求沉积 
            let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
            if(results.length>0){
                callback(results);
            }else {
              this.restaurants=[];
          //调用的后台接口
          this.$axios
            .post(
              this.$config.serverUrl + "api/ZaoLinJiHua/GetNAMEData",
              JSON.stringify(queryString),
              {
                headers: {
                  "Content-Type": "application/json"
                }
              }
            )
            .then(res => {
              let data = res.data;
                if(data=="没有数据"){
                     var json = {
                    "value": "没有数据",
                  };
                  this.restaurants.push(json);
                  callback(this.restaurants);
                }
              for (var i = 0; i < data.length; i++) {
                  data[i].index = i;
                  data[i].xuhao = i+1;
                  var json = {
                    "value": data[i].jiHuaMingCheng,
                  };
                  this.restaurants.push(json);
                  callback(this.restaurants);
                }
              
            })
            .catch(error => {
              console.log(error);
              if(error=="没有数据"){
                     var json = {
                    "value": "没有数据",
                  };
                  this.restaurants.push(json);
                  callback(this.restaurants);
                }
            });
          
        } 
      },
    //选择行政级别
    currentJB(Value) {
      this.getchildData(Value);
    },
    // 获取父级行政代码
    getchildData(Value) {
      this.$axios
        .post(
          this.$config.serverUrl + "api/SystemSet/getchildData",
          JSON.stringify(Value),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          let data = res.data.result;
          var i = res.data.l_ZQJB;

          if (i == 2) {
            this.parentoptions_shi = [];
            this.Form.shi = "";
            this.Form.xian = "";
            this.parentoptions_shi = data;
            this.Form.l_ZQJB = "";
            this.Form.c_ZQCODE = "";
          } else if (i == 3) {
            this.parentoptions_xian = [];
            this.Form.xian = "";
            this.parentoptions_xian = data;
            this.Form.l_ZQJB = "";
            this.Form.c_ZQCODE = "";
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取工程类别
    getgongchenData() {
      this.$axios
        .post(
          this.$config.serverUrl + "api/Dictionary/GetDictionary",
          JSON.stringify("PROJECTTYPE"),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          if (res.status) {
            let data = res.data;
            this.gongchengoptions = [];
            this.gongchengoptions = data;
          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取父级行政代码
    getParentData(selVal) {
      this.$axios
        .post(
          this.$config.serverUrl + "api/SystemSet/getParentData",
          JSON.stringify(selVal),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          if (res.status) {
            let data = res.data;
            this.parentoptions = [];
            this.parentoptions = data;
          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取新增政区数据
    GetZQData(resolve) {
      return this.$axios
        .post(this.$config.serverUrl + "api/SystemSet/GetZQData", {
          headers: {
            "Content-Type": "application/json"
          }
        })
        .then(res => {
          if (res.status) {
            var selects = [];
            let data = res.data;
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
              var jsonselect = {
                value: data[i].c_ZQCODE,
                label: data[i].c_ZQNAME
              };
              selects.push(jsonselect);
              this.parentoptions = [];
              this.parentoptions = selects;
            }
          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取省级数据
    GetShengDataByCode(ZQcode) {
      this.$axios
        .post(
          this.$config.serverUrl + "api/SystemSet/GetShengDataByCode",
          JSON.stringify(ZQcode),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          if (res.status) {
            var tree = [];
            this.dataprop=[];
            let data = res.data;

            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
              var json = {
                name: data[i].c_ZQNAME,
                code: data[i].c_ZQCODE,
                children: []
              };
              tree.push(json);
              this.dataprop.push(json)
            }
            this.dic = tree;
            //return resolve(tree);
          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取省级以下数据
    GetZQDataByCode(ZQcode, datatree) {
      this.$axios
        .post(
          this.$config.serverUrl + "api/SystemSet/GetZQDataByCode",
          JSON.stringify(ZQcode),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          if (res.status) {
            var tree = [];
            let data = res.data;
            datatree.children=[];
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
              var json = {
                name: data[i].c_ZQNAME,
                code: data[i].c_ZQCODE,
                children: []
              };
              tree.push(json);
              datatree.children.push(json);
            }

            if (data[0].l_ZQJB == 2) {
              this.dic_shi = tree;
            } else if (data[0].l_ZQJB == 3) {
              this.dic_xian = tree;
            } else if (data[0].l_ZQJB == 4) {
              this.dic_xiang = tree;
            } else {
              this.dic_cun = tree;
            }
            //
            if (data[0].l_ZQJB == 2) {
              var ZQJB = 2;
            } else {
              var ZQJB = 3;
            }
            this.$axios
              .post(
                this.$config.serverUrl + "api/ZaoLinJiHua/GetDataByCode",
                JSON.stringify({
                  code: ZQcode,
                  ZQJB: ZQJB
                }),
                {
                  headers: {
                    "Content-Type": "application/json"
                  }
                }
              )
              .then(res => {
                let data = res.data.list;
                let totle = res.data.totle;
                for (var i = 0; i < data.length; i++) {
                  data[i].index = i;
                  data[i].xuhao = i+1;
                }
                this.form.totle = totle;
                this.alltableData = [];
                this.alltableData = data;
                this.total = data.length;
                this.tableData = this.alltableData.slice(0, 10);
                this.currentPage1 = 1;
              })
              .catch(error => {
                console.log(error);
              });
            //return resolve(tree);
          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 保存编辑造林数据
    SaveEditData(Form) {
      this.$axios
        .post(
          this.$config.serverUrl + "api/ZaoLinJiHua/SaveEditData",
          JSON.stringify(Form),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          let data = res.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    //新增造林数据
    SaveAddData(form) {
      return this.$axios
        .post(
          this.$config.serverUrl + "api/ZaoLinJiHua/SaveAddData",
          JSON.stringify(form),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .catch(error => {
          console.log(error);
        });
    },
    // 删除数据
    delectEditData(id) {
      return this.$axios
        .post(
          this.$config.serverUrl + "api/ZaoLinJiHua/DelectEditData",
          JSON.stringify(id),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .catch(error => {
          console.log(error);
        });
    },
    search() {
      var zqname=this.select_word;
       if(zqname==""){
         this.getgongchenData();
       }else{
       this.$axios
        .post(
          this.$config.serverUrl + "api/ZaoLinJiHua/GetZQDataByName",
          JSON.stringify(zqname),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          if (res.status) {
            let data = res.data;
            this.alltableData = [];
            this.alltableData = data;
            this.total = data.length;
            var index = Math.ceil(this.total / 10);
            this.currentPage1 = index;
            this.handleCurrentChange(index);
            //this.tableData = this.alltableData.slice(0, 9);
          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
          //	this.$message.error(error);
          //callback(new Error(error));
        });
       }

    },
    handleEdit(index, row) {
      this.idx = index;
      const item = this.tableData[index];
      this.Form =item;
      this.editVisible = true;
    },
    handlecheck(index, row) {
      this.idx = index;
      const item = this.tableData[index];
     //进工程概况,参数id
     var id=item.id;
     this.$router.push({
	            			name:'ProjectOverview',
	            			params:{
	            					JH_id:id,
	            					
	            				}
	            			});
    },
    handleAdd() {
      this.GetZQData();
      this.$refs.form.resetFields();
      if(this.form.xian!=""){
        this.form.zqId=this.form.xian;
      }else{
        if(this.form.shi!=""){
          this.form.zqId=this.form.shi;
        }else{
          this.form.zqId=this.form.sheng;
        }
      }
      this.dialogVisible = true;
    },
    handleDelete(index, row) {
      this.idx = index;
      const item = this.tableData[index];
      this.Form =item ;
      this.delVisible = true;
    },
    delAll() {
      const length = this.multipleSelection.length;
      let str = "";
      this.del_list = this.del_list.concat(this.multipleSelection);
      for (let i = 0; i < length; i++) {
        str += this.multipleSelection[i].name + " ";
      }
      this.$message.error("删除了" + str);
      this.multipleSelection = [];
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 保存编辑
    saveEdit() {
      this.Form.Token = sessionStorage.getItem("token");
      this.SaveEditData(this.Form);
      this.$message.success(`修改第 ${this.idx + 1} 行成功`);
      this.alltableData[this.Form.index] = this.Form;
      this.editVisible = false;
      if(this.Form.index==0){
      var val = 1;
      this.handleCurrentChange(val);
      }else{
         var val = Math.ceil(this.Form.index / 10);
      this.handleCurrentChange(val);
      }
     
      //
    },
    // 保存新增
    saveAdd() {
      this.form.Token = sessionStorage.getItem("token");
      this.SaveAddData(this.form).then(res => {
        this.$axios
          .post(
            this.$config.serverUrl + "api/ZaoLinJiHua/GetDataByCode",
            JSON.stringify({
              code: this.form.sheng,
              ZQJB: 2
            }),
            {
              headers: {
                "Content-Type": "application/json"
              }
            }
          )
          .then(res => {
            let data = res.data;
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
            }
            this.alltableData = [];
            this.alltableData = data;
            this.total = data.length;
            var index = Math.ceil(this.total / 10);
            this.currentPage1 = index;
            this.handleCurrentChange(index);
          })
          .catch(error => {
            console.log(error);
          });
        this.form.sheng = "";
        this.form.shi = "";
        this.form.xian = "";
        this.form.zaoLinDate = "";
        this.form.gongChengLeiBie = "";
        this.form.jiHuaMianJi = "";
      });
      this.$message.success(`新增第 ${this.idx + 1} 行成功`);

      this.dialogVisible = false;
    },
    // 确定删除
    deleteRow() {
      this.delectEditData(this.Form.id).then(res => {
        this.$axios
          .post(
            this.$config.serverUrl + "api/ZaoLinJiHua/GetDataByCode",
            JSON.stringify({
              code: this.Form.sheng,
              ZQJB: 2
            }),
            {
              headers: {
                "Content-Type": "application/json"
              }
            }
          )
          .then(res => {
            let data = res.data.list;
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
            }
            this.alltableData = [];
            this.alltableData = data;
            this.total = data.length;
            var index = Math.ceil(this.Form.index / 10);
            this.currentPage1 = index;
            this.handleCurrentChange(index);
          })
          .catch(error => {
            console.log(error);
          });
      });
      this.$message.success("删除成功");
      this.delVisible = false;
      //
    },

    handleNodeClick(data, node, resolve) {
      if (node.level > 0) {
        var ZQcode = node.data.code;
        this.zq_name=node.data.name;
        var datatree=data;
        this.GetZQDataByCode(ZQcode, datatree);
        return resolve([]);
      }
    },
  }
};
</script>

<style scoped>
@import "../common/css/proCommon.css";
div.formBottom{
	margin-top:16px;
}
    .demo-drawer-footer{
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        border-top: 1px solid #e8e8e8;
        padding: 10px 16px;
        text-align: right;
        background: #fff;
    }
</style>


