<template>
	<div class="main">
		<div id="mainMap">        
      
	   </div>
	</div>
    
</template>

<script>
import bus from "./Header/bus";
import MapLib from "@module/olMap/src/mapHelper/index.js";
export default {
  data() {
    return {};
  },
  components: {},
  computed: {},
  created() {
    bus.$on("collapse", msg => {
      this.collapse = msg;
    });

    // 只有在标签页列表里的页面才使用keep-alive，即关闭标签之后就不保存到内存中了。
    bus.$on("tags", msg => {
      let arr = [];
      for (let i = 0, len = msg.length; i < len; i++) {
        msg[i].name && arr.push(msg[i].name);
      }
    });
  },
  activated() {},
  mounted: function() {
    var factory = MapLib.mapHelperFactory;
    var layers = MapLib.layers;
    var enums = MapLib.enums;
    var extensions = MapLib.extensions
    var mapHelper = factory.createMapHelper();
    var eviaEx = new extensions.eviaEx(
      {
        url: this.$config.eviaSearchUrl,
        translate: true,
        ignore: ["id", "gid"],
        date: "2009"
      },
      { ol: ol }
    );
    mapHelper.init(
      {
        // crateDefBarControl: false
        // layers: [layers.test()]
        exBarOpt: [
          {
            id: enums.defBarId.mainTopRight,
            opt: {
              exControlOpt: [
                {
                  id: enums.defControlId.identify,
                  opt: {
                    events: {
                      exSearch: eviaEx.identify
                    }
                  }
                }
              ]
            }
          }
        ],
         target: "mainMap",
      },
     
      { ol: ol }
    );

    //图层
    mapHelper.addLayer(enums.mapId.GoogleImage, layers.getGoogleImage());
    mapHelper.addLayer(
      "evia",
      layers.getEviaLayer(null, null, this.$config.eviaLayer)
    );
  },
  deactivated() {},
  methods: {

  }
};

</script>
<style scoped="scoped">
div.main{
	width:100%;
	height:100%;
}
div#map {
  margin: -10px;
}
#map {
  height: calc(100% - 30px);
  width: 100%;
  position: absolute;
  z-index: 10;
}
div.control {
  position: absolute;
  z-index: 20;
}
</style>



