<template>
	
	
    <div class="table">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-lx-cascades"></i>{{this.$route.meta.title}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
        	<div class="table">
        		 <div class="handle-box">
	                <el-select v-model="proState" placeholder="选择类型" class="handle-select mr10">
	                   <el-option
					      label="全部"
					      value="">
					    </el-option>
	                   <el-option
					      v-for="item in selectProState"
					      :key="item.code"
					      :label="item.name"
					      :value="item.code">
					    </el-option>
	                </el-select>
	                <el-input v-model="keyword" placeholder="搜索关键词" class="handle-input mr10"></el-input>
	                <el-button type="primary" icon="el-icon-search" @click="getData">搜索</el-button>
	                <el-button class="rightRefresh" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
	                <el-button type="primary" icon="el-icon-plus" @click="addData">添加</el-button>
	            </div>
	            <el-table 
	            	v-loading="loading"
	            	:data="tableData" 
	            	highlight-current-row
	            	border 
	            	class="table" 
	            	ref="multipleTable" 
	            	@selection-change="handleSelectionChange">
	                <el-table-column prop="createDate" label="日期" 
	                	:formatter="formatter"
	                	 align="center" width="156">
	                </el-table-column>
	                <el-table-column prop="xmbh" label="项目编号" align="center" width="130">
	                </el-table-column>
	                <el-table-column prop="xmmc" label="项目名称"  align="center" >
	                </el-table-column>
	                <el-table-column
				      prop="state_Name"
				      align="center"
				      label="状态"
				      width="120"
				>
				      <template slot-scope="scope">
				        <Tag
				          :color="getState(scope.row.state_Name)"
				          type="dot"
				          fade>{{scope.row.state_Name}}</Tag>
				      </template>
				    </el-table-column>
	               <!--本页表格数据筛选
	               	<el-table-column
				      prop="state"
				      align="center"
				      label="进度"
				      width="100"
				      :filters="[{ text: '待审核', value: '待审核' }, { text: '待提交', value: '待提交' }]"
				      :filter-method="filterTag"
				      filter-placement="bottom-end">
				      <template slot-scope="scope">
				        <el-tag
				          :type="scope.row.state === '待审核' ? 'primary' : 'success'"
				          disable-transitions>{{scope.row.state}}</el-tag>
				      </template>
				    </el-table-column>-->
	                <el-table-column label="小班数" width="180" align="center">
	                    <template slot-scope="scope">
						     <el-tooltip 
	                        	class="item" 
	                        	effect="dark" 
	                        	placement="top"
	                        	>
	                        	<div slot="content" v-if="scope.row.state_Name">{{scope.row.state_Name}}{{scope.row.currentStatesCount || 0}}个小班，总共{{scope.row.totalCount || 0}}个小班</div>
						       <el-button
						       	 @click="toSubcompartment(scope.row,'watch')">{{scope.row.currentStatesCount || 0}}/{{scope.row.totalCount || 0}}</el-button>
						    </el-tooltip>
	                    </template>
	                </el-table-column>
	                <el-table-column label="操作" 
	                	:formatter="control"
	                	align="center" width="300">
	                </el-table-column>
	            </el-table>
	            <div class="pagination">
					 <el-pagination
				      @size-change="handleSizeChange"
				      @current-change="handleCurrentChange"
				      :current-page="currentPage"
				      :page-sizes="[5, 10, 15, 20]"
				      :page-size="pageSize"
				      layout="total, sizes, prev, pager, next, jumper"
				      :total="totalPage">
				    </el-pagination>
	            </div>
        	</div>
        </div>
		

		<!--抽屉框，查看-->
		<Drawer
            :title="cantrolType"
            v-model="drawer"
            width="720"
            :mask-closable="false"
            :styles="styles"
        >
	    	<div id="basicmsg">
	    		<div class="table" >
		        	<Tabs  v-model="activeName" on-click="handleClick">
					    <TabPane  label="项目基本信息" name="first">
					    	 <el-form 
					    	 	class = "formDiv"
					    	 	ref="Form"
					    	 	disabled
					    	 	size = "small"
					    	 	:model="formData">
				                <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="项目名称:" label-position="top" class="noMargin" prop="xmmc">
				                           <!-- <el-input v-model="formData.name" placeholder="please enter user name" />-->
				                            <el-input v-model="formData.xmmc"  ></el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目编号:" label-position="top" class="noMargin" prop="xbh">
				                            <el-input v-model="formData.xmbh"  >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                <el-row :gutter="32">
				                    <el-col :span="12">
				                    	<el-form-item label="造林计划:" label-position="top" prop="jH_Id">
				                            <el-select 
				                            	v-model="formData.jH_Id" 
				                            	class = "selectDiv"
				                            	placeholder="请选择">
				                           		 <el-option
											      v-for="item in JH_IDOptions"
											      :key="item.id"
											      :label="item.jiHuaMingCheng"
											      :value="item.id">
											    </el-option>
				                            </el-select>
				                        </el-form-item>
				                    	
				                    	
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目类别:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.xmlb"  >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                 <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="投资文件文号:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.tzwjwh"  >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目总投资:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.xmztz" >
				                            	<template slot="append">万元</template>
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                <el-row :gutter="32">
				                	<el-col :span="12">
				                        <el-form-item label="工程类别:" label-position="top" class="noMargin">
				                            <el-select 
				                            	v-model="formData.gclb_Name" 
				                            	class = "selectDiv">
				                            </el-select>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="营造林方式 :" label-position="top" class="noMargin">
				                            <el-select 
				                            	v-model="formData.yzlfs_Name" 
				                            	class = "selectDiv">
				                            </el-select>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="建设内容:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.jsnr" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="建设规模:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.jsgm" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                       <el-form-item label="项目实施主体:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.xmzt" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目负责人:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.xmfzr" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                 </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                       <el-form-item label="设计单位:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.xmsjdw" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="设计日期:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.xmsjrq">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    
				                 </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                        <el-form-item label="设计证书等级:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.SJZSDJ" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                 	<el-col :span="12">
				                        <el-form-item label="设计单位负责人:" label-position="top" class="noMargin">
				                            <el-input v-model="formData.SJDWFZR" >
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    
				                 </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                        <el-form-item label="描述" label-position="top" class="noMargin">
						                    <el-input type="textarea" v-model="formData.bz" :rows="4" />
						                </el-form-item>
				                    </el-col>
				                 	<el-col :span="12">       
						                <el-form-item label="审批意见" label-position="top" class="noMargin">
						                    <el-input type="textarea" v-model="formData.spyj" :rows="4" placeholder="请填写" />
						                </el-form-item>
				                    </el-col>
				                    
				                 </el-row>
				            </el-form>
					    </TabPane>
					    <TabPane label="项目材料" name="second">
					    	<upload-file></upload-file>
					    	
					    </TabPane>
					    <TabPane label="造林小班" name="third">
					    	<xiaoban-list v-bind:XMBH="formData.xmbh" v-bind:state="formData.state" :drawer.sync="drawer" ></xiaoban-list>
					    	
					    </TabPane>
					    <TabPane label="项目经费" name="fourth">项目经费</TabPane>
					 </Tabs >
					 <span>版本号:<span class="blueText">{{formData.version}}</span>；
					 	<span class="blueText">{{formData.createUser_Name}}</span>于
					 	{{formData.createDate }}&nbsp;创建，
					 	<span class="blueText">{{formData.updateUser_Name}}</span>{{ formData.updateDate }}&nbsp;修改</span>
					 <div class="demo-drawer-footer">
				        <Button type="primary" style="margin-right: 8px" @click="drawer = false">关闭</Button>
				      </div>
	        	</div>
	        	
	    		
	    		
	    	</div>
       </Drawer>    
       <!--编辑项目基本信息框，查看-->
		<Modal v-model="baseMsg" title="填写项目基本信息">
			<el-form ref="baseForm" :model="formData" :rules="formDatatValidate" label-width="80px">
				<el-form-item label="项目名称" prop="xmmc">
					<el-input v-model="formData.xmmc" placeholder="项目名称"></el-input>
				</el-form-item>
				<el-form-item label="项目编号" prop="xmbh">
					<el-input v-model="formData.xmbh" placeholder="项目编号"></el-input>
				</el-form-item>
				<el-form-item label="所属计划" prop="jH_Id">
					<el-select v-model="formData.jH_Id" class="selectDiv" :loading="loading">
						<el-option v-for="item in JH_IDOptions" :key="item.id" :label="item.jiHuaMingCheng" :value="item.id">
						</el-option>
					</el-select>
		
				</el-form-item>
			</el-form>
			<span slot="footer">
			                <Button @click="baseMsg = false">取 消</Button >
			                <Button  type="primary" @click="submitBase('baseForm')">确 定</Button >
			            </span>
		</Modal>
    </div>
</template>

<script>
	import common from "../common/js/common.js"
	import uploadFile from "../common/vue/Upload"
	import xiaobanList from "../common/vue/XiaoBanList.vue"
    export default {
        name: 'basetable',
        components:{
           uploadFile:uploadFile,
           xiaobanList,
        },
        data() {
            return {
                tableData: [],
                loading:true,
                currentPage: 1,
                pageSize:5,
                totalPage:0,
                baseMsg:false,
                cantrolType:"添加项目信息",
                activeName:"first",
                multipleSelection: [],
                selectProState: [],
                projectType: [],
                afforestationType: [],
                check:true,
                proState:null,
                drawer:false,
                styles: {
                    height: 'calc(100% - 55px)',
                    overflow: 'auto',
                    fontSize:"16px",
                    paddingBottom: '53px',
                    position: 'static'
                },
                formData: {
                    xmmc: '',
                    xmbh:"",
                    jH_Id:"",
                    xmlb: '',
                    state:"",
                    gclb: '',
                    yzlfs: '',
                    gcssnd:"",
                    tzwjwh:"",
                    xmztz:"",
                    jsnr:"",
                    jsgm:"",
                    xmzt: '',
                    xmfzr:'',
                    xmsjdw: '',
                    xmsjrq:'',
                    bz: ''
                },
                JH_id:NaN,
                JH_IDOptions:[],
                projectId:"",
                keyword: '',
                del_list: [],
                delVisible: false,
                rowId:"",
                idx: -1,  //选中页的行数
                formDatatValidate:{
                	xmmc: [
                        { required: true, message: '请输入项目名称', trigger: 'blur' }
                    ],
                    xmbh: [
                        { required: true, message: '请输入项目编号', trigger: 'blur' },
                    ],
                    jH_Id: [
                        { required: true, message: '请选择工程所属计划', trigger: 'blur' },
                    ],
                }
                
            }
        },
        created(){
        	 this.getDic().then(()=>{
        	 	
        	 	this.getData();
        	 })
        	
           
        },
        mounted() {
            
        },
        computed: {
        	 watchZLJH(){
        	 	if(this.$route.name == "ProjectOverview"  &&  this.JH_id != this.$route.params.JH_id) {
					this.JH_id = this.$route.params.JH_id;
					this.formData.jH_Id = parseInt(this.JH_id);
					isNaN(this.formData.jH_Id)? this.formData.jH_Id =null:null;
					console.log(this.formData.jH_Id);
					this.getData();
						
				}
        	}
        },
        watch:{
			watchZLJH() {
			
				     return this.$route.params.JH_id;
			}
		},
        methods: {
        	getState(state){
        		let str ="";
        		switch(state){
        			
        			case "未提交":
        			str =   "primary";
        			break;
        			case "审核中":
        			str =   "warning";
        			break;
        			case "待验收":
        			str =   "#87e8de";
        			break;
        			case "待核查":
        			str =   "success";
        			break;
        			case "已结项":
        			str =   "default";
        			break;
        			case "已退回":
        			str =   "error";
        			break;
        			default:
        			str =   "primary";
        		}
        		return str;
        	},
        	handleSizeChange(val){
        		this.pageSize = val ;
        		this.getData();
        	},
            // 分页导航
            handleCurrentChange(val) {	
                this.currentPage = val;
                this.getData();
            },
            addData(){
            	this.baseMsg = true ; 
            //	this.formData = {};
            },
            getDic(){
            	
            	this.$store.dispatch("getDics",["ProjectState","PROJECTTYPE","AFFORESTATIONTYPE"]).then(data=>{
	            	for(let obj of data){
							switch(obj.type){
								case "ProjectState":
								this.selectProState  = obj.data;
								break;
								case "PROJECTTYPE":
								this.projectType  = obj.data;
								break;
								case "AFFORESTATIONTYPE":
								this.afforestationType  = obj.data;
								break;
							}
						}
	            });
	            return this.$axios.post(this.$config.serverUrl+"api/ZaoLinJiHua/GetZljhData", 
	                JSON.stringify(window.sessionStorage.getItem('token')),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	                	console.log(res);
	                	if(res && res.data){
	                		this.JH_IDOptions = res.data;
	                	}else{
	                		this.$message.error("数据错误");
	                	}
	                }).catch(error=>{
	                	this.$message.error(error);
	                })  
            },
            refresh(){  //重置

               this.JH_id = "";
			   if(this.$route.params.JH_id){
			   		this.$route.params.JH_id = null;  //JH_id为  ""  params.JH_id为null 避免相同
			   }else{
			   		
				   	this.keyword = "";
				   	this.proState = null;
	            	this.currentPage= 1;
	                this.pageSize=5;
	                this.getData();
			   }
            },
            // 获取 easy-mock 的模拟数据
            getData() {
                // 开发环境使用 easy-mock 数据，正式环境使用 json 文件
                if (process.env.NODE_ENV === 'development') {  //开发环境
                    this.url = 'https://easy-mock.com/mock/5bd9623b425b8a0c39a5b280/example/getForestData';
                };
                
                if(!isNaN(this.JH_id)){   //避免和computed重复
                	var toService ={
                	    keyword:$.trim(this.keyword),
                	    jhid:this.JH_id,
                	    token:window.sessionStorage.getItem('token'),
                	    isSubcompartmentInfo: true,
	                	pageQueryInfo:{
			                	pageSize: this.pageSize,
			    				pageIndex: this.currentPage
	                	},
	                };
	                this.proState ? toService.states = [this.proState]:null; //没有不传
	                console.log(JSON.stringify(toService));
	                this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/GetYZL_ZLXM", 
	                JSON.stringify(toService),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	                	console.log(res);
	                    this.tableData = res.data.resultValue;
	                    this.totalPage=res.data.count;
	                    this.loading= false;
	                }).catch(error=>{
	                	this.$message.error(error);
	                	})
                }
                
            },
            toSubcompartment(data,type){
            	
            	let ToState = null;
            	if(data.state == 1 || data.state == 2){
            		ToState = 3;
            	}else if(data.state == 3 || data.state == 4){
            		ToState = data.state +1;
            	}else if(data.state == 5){
            		ToState = 999;
            	}else{
            		ToState = null;
            	}
            	this.drawer = false;
            	this.$store.commit("setXmbh",{
            		xmbh:data? data.xmbh : this.XMBH,
            		ToState:ToState,
            	});
            	this.$router.push({	
            						name:"SubcompartmentWorkSpace",
            						params:{
            							type,
            							xmbh: data ? data.xmbh : this.XMBH,
            							ToState:ToState,
            							}
            						});
            },
            turnTo(path,obj){
            	//监听变化
            	this.$store.commit("setPro",{
	            					proID:obj.id,
	            					name:obj.xmmc,
	            			});
            //	this.$router.replace("/icon");
				this.$router.push({name:path,params:{proID:obj.id,name:obj.xmmc,ticket:path}});
            },
            search() {
                this.getData();
            },
            selectable(row,index){
            	if(row.state_Name =='未提交' || row.state_Name =='已退回'){
            		return true;
            	}else{
            		return false;
            	}
            },
			formatter(row,ele){
 				return common.formatDate(row.createDate);
			},
			control(row, column, cellValue, index){
				let path = "";
				let name ="";
				let turnTo = true;
				
				switch(row.state_Name){
					case "未提交":
						path="ForestDesign";
						name="转至提交";
					break;
					case "已退回":
					path="ForestDesign";
					name="转至提交";
					break;
					case "审核中":
					path="ForestApproval";
					name="转至审核";
					break;
					case "待验收":
					path="ForestAccept";
					name="转至验收";
					break;
					case "待核查":
					path="ForestCheck";
					name="转至核查";
					break;
					case "已结项":
					path=" ";
					name="转至...";
					turnTo = false;
					break;
					
				}
				return (
					<div>
						<el-button  type="text" icon="fa fa-eye" on-click={()=>{this.handleEdit(index, row)}}>&nbsp;查看</el-button>
						<el-button type="text" 
						disabled = {!turnTo}
						icon="fa fa-hand-o-right" on-click={()=>{this.turnTo(path, row)}}>&nbsp;{name}</el-button>
					</div>
					
					);
			},
            handleEdit(index, row) {
            	//深拷贝，，表单与table解绑，，，，主要是解决重置表单后，表格的数据跟着被清空
            	this.tableData[this.idx] = JSON.parse(JSON.stringify(this.formData));
            	
                this.idx = index;
                //重置表单
                this.$refs["Form"].resetFields();
                let  item = this.tableData[index];
                console.log(index);
                console.log(this.tableData[index].createUser_Name);
                console.log(item.createUser_Name);
                item.createDate = common.formatDate(item.createDate);
                item.updateDate = common.formatDate(item.updateDate);
                item.type = item.type + "";
                item.method = item.method + "";
                this.formData = item;
      //          console.log(this.formData);
                this.cantrolType = "修改项目信息";
                this.drawer = true;
                
            },
            handleDelete(index, row) {
                this.idx = index;
                this.delVisible = true;
            },
            delAll() {
            	if(this.multipleSelection.length<1){
            		this.$message.error("您未选择");
            	}else{
            		this.delVisible = true;
            	}
            },
            delData(arr){
            	return this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/DeleteYZL_ZLXM", 
	                JSON.stringify(arr),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	                	console.log(res);
	                	 this.$message.success('删除成功');
	                }).catch(error=>{
	                	this.$message.error(error);
	                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            submitFrom(name){
            	this.$refs[name].validate((valid) => {
                    if (valid) {
                    	console.log( this.formData);
                    	let value = this.formData;
	                    	value.token = this.$store.state.token;
	                    	value.state = 2;
	                		!value.id ? value.id =this.rowId:null;
                    	this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/UpdateYZL_ZLXM", 
			                JSON.stringify(value),
			                	{headers: {'Content-Type': 'application/json'}}
			                ).then((res) => {
			                	
			                	this.$message.success("保存成功");
			                }).catch(error=>{
			                
                				this.$message.error(error);
                			})
                
                    } else {
                        this.$Message.error('请正确填写');
                    }
                })
            },
            submitBase(name){
            	console.log(this.formData);
            	this.$refs[name].validate((valid) => {
                    if (valid) {
                    	this.cantrolType = "添加项目信息";
                    	this.drawer = true;
                    	this.baseMsg =false;
                    	console.log(this.formData);
                    	this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/PostYZL_ZLXM", 
			                JSON.stringify({
			                	token:window.sessionStorage.getItem('token'),
			                	xmbh:this.formData.xmbh,
			                	state:2,
			                	xmmc:this.formData.xmmc,
			                	JH_id :this.formData.jH_Id,
			                }),
			                	{headers: {'Content-Type': 'application/json'}}
			                ).then((res) => {
			                	this.getData();
			                	this.$Message.success('添加成功');
			                	this.rowId = res.data.id;
			                	console.log(res.data.id);
			                }).catch(error=>{
                				this.$message.error(error);
                			})
                    	
                    } else {
                        this.$Message.error('请正确填写');
                    }
                })
            },
            // 确定删除
            deleteRow(){
                this.delVisible = false;
                let delArr= [];
                if(this.multipleSelection.length>0){  //删除多个
                	delArr = [];
            		for(let i of this.multipleSelection){
            			delArr.push(i.id)
            		}
            		this.multipleSelection = [];
                }else{
	                delArr=[this.tableData[this.idx].id];
                }   
                this.delData(delArr).then(()=>{
            			this.getData();
            		})
            },
            handleClick(tab, event) {
		        console.log(tab, event);
		      }
            
        }
    }

</script>

<style scoped>
 @import "../common/css/proCommon.css";
</style>



