//初始化toolBar所在的容器
function initControlBarContainer() {
    this.container.append(controlBarContainerHtml);
}

function _getExOpt(id, exOpt) {
    // return [].concat.apply([], exOpt.filter(x => x.id == id).map(x => x.opt))
    return (exOpt.filter(x => x.id == id)[0] || {}).opt;
}
//添加几个默认的bar
function createDefControlBar() {
    //添加几个默认
    var bar = controlBarFactory.baseTopRightBar();
    var exOpt = this.options.exBarOpt ? this.options.exBarOpt : []

    this.addControlBar(bar, _getExOpt(defConBarId.mainTopRight, exOpt));
}

//添加默认常用的ol组件
function createDefOlControl() {
    var self = this;
    var ol = self.injects.ol;
    //全屏
    var fullScreen = new ol.control.FullScreen();
    self.map.addControl(fullScreen);

    //比例尺
    if (!document.getElementById(self.options.olControl.scaleLine.target)) {
        self.container.append('')
    }
    var scaleLineControl = new ol.control.ScaleLine(self.options.olControl.scaleLine);
    self.map.addControl(scaleLineControl);
    self.map.controls = (ol.control.defaults({
        attributionOptions: {
            collapsible: false
        }
    }).extend([
        scaleLineControl
    ]))
    //鼠标移动显示经纬度
    var _opt = $.extend(false, {}, self.options.olControl.mousePosition)

    // 显示鼠标位置信息的目标容器
    _opt.target = document.getElementById(_opt.target);
    if (!_opt.target) {
        self.container.append(`<div id="${self.options.olControl.mousePosition.target}" style="position: absolute;bottom: 1rem;left: 17rem;z-index: 9999;"></div>`)
        _opt.target = document.getElementById(self.options.olControl.mousePosition.target);
    }
    _opt.coordinateFormat = ol.coordinate.createStringXY(_opt.coordinateFormat); //坐标格式

    //TODO: remove me if addControl() work
    self.map.on(eventName.mouseOver, function (e) {
        var value = e.coordinate
        if (this.getView().getProjection().getCode() != "EPSG:4326") {
            value = ol.proj.toLonLat(e.coordinate);
        }
        _opt.target.innerHTML = _opt.coordinateFormat(value)
    })

    // var mousePositionControl = new ol.control.MousePosition(_opt);
    // self.map.addControl(mousePositionControl); //FIXME: I HAVE NO IDEA that why this line dosn't work

    // //鼠标移动显示经纬度
    // 	var mousePositionControl = new ol.control.MousePosition({
    // 		coordinateFormat: ol.coordinate.createStringXY(4), //坐标格式
    // 		projection: 'EPSG:4326', //地图投影坐标系
    // 		className: 'custom-mouse-position', //坐标信息显示样式
    // 		// 显示鼠标位置信息的目标容器
    // 		target: document.getElementById('mouse-position'),
    // 		undefinedHTML: '&nbsp' //未定义坐标的标记
    // 	});

}

function addDefLayers() {
    var self = this;
    //临时绘制图层
    self.addLayer(mapId.tmpDrawLayer, layers.getVector());
}

//设置额外的空间参考 TODO: 考虑日后移出去
function setExProj() {
    var self = this

    var ol = self.getOl();
    var _opt = self.options.projOpt;

    ol.proj.setProj4(proj4)

    for (let i = 0; i < _opt.exProj.length; i++) {
        const defineOpt = _opt.exProj[i];
        proj4.defs(defineOpt.id, defineOpt.def)
    }




}


