    //justOneAlive => true 从这里走的能确保是全局触发一个
    addListener: function (eventName, callback, justOneAlive = true) {
        if (!eventName) {
            throw ' eventName must not null'
        }
        if (!callback) {
            throw ' callback must not null'
        }
        var self = this;
        var curEvent = self.events.filter(x => x.id == eventName)[0]
        if (!curEvent) {
            self.events.push({
                id: eventName,
                callback: [],
                type: 'map'
            })
            curEvent = self.events.filter(x => x.id == eventName)[0]
        }
        if (justOneAlive) {
            if (curEvent.callback && curEvent.callback.length > 0) {
                for (let i = 0; i < curEvent.callback.length; i++) {
                    var _callback = curEvent.callback[i];
                    self.map.un(eventName, _callback)
                }
                curEvent.callback = [];
            }
        }
        curEvent.callback.push(callback);
        return self.map.on(eventName, callback)

    },
    removeListener(eventName, callback) {
        var self = this
        var ol = self.injects.ol;
        if (comLib.isObj(eventName)) { // 传的是对象说明是listener 直接 un
            ol.Observable.unByKey(eventName);
        } else {  // string 说明是 name
            if (callback) {
                self.map.un(eventName, callback)
            } else {
                var events = self.events.filter(x => x.id == eventName)
                events.forEach(x => {
                    self.map.un(eventName, x.callback[0], self)
                })
            }
        }
    },
    //绘制
    draw: function (callbacks, drawType, source, options = {}) {
        if (!callbacks) {
            throw 'callbacks is null, can not draw'
        }
        var self = this;
        var ol = this.injects.ol;
        var map = this.map;

        if (self.drawObj) {
            map.removeInteraction(self.drawObj)
        }
        var _source = source || self.getLayer(mapId.tmpDrawLayer).getSource()
        self.drawObj = new ol.interaction.Draw({
            //绘制的数据源
            source: _source,
            //绘制类型
            type: drawType || 'Polygon',
            //样式
            style: new ol.style.Style({
                fill: new ol.style.Fill({
                    color: 'rgba(255,255,255,0.2)'
                }),
                stroke: new ol.style.Stroke({
                    color: 'rgba(0,0,0,0.5)',
                    lineDash: [10, 10],
                    width: 2
                }),
                image: new ol.style.Circle({
                    radius: 5,
                    stroke: new ol.style.Stroke({
                        color: 'rgba(0,0,0,0.7)'
                    }),
                    fill: new ol.style.Fill({
                        color: 'rgba(255,255,255,0.2)'
                    })
                })
            })
        });

        if (callbacks.startDraw) {
            self.drawObj.on('drawstart', function (evt) {
                return callbacks.startDraw(evt, _source)
            })
        }
        if (callbacks.endDraw) {
            self.removeListener(eventName.click, eve_drawClick);
            self.removeListener(eventName.doubleClick, eve_drawDbClick);
            self.drawObj.on('drawend', function (evt) {
                if (options.onceCall) {
                    self.map.removeInteraction(self.drawObj);
                }
                self.removeListener(eve_drawClick)
                self.removeListener(eve_drawDbClick)
                return callbacks.endDraw(evt, _source)
            })
        }

        var eve_drawClick = self.addListener(eventName.click, function (evt) {
            if (comLib.isFunc(callbacks.click)) {
                callbacks.click(evt)
            }
        })
        var eve_drawDbClick = self.addListener(eventName.doubleClick, function (evt) {
            if (comLib.isFunc(callbacks.doubleClick)) {
                callbacks.doubleClick(evt)
            }
        })


        map.addInteraction(self.drawObj);

        return self.drawObj;
    },
