          break;
        case 2:
          this.Form.l_PARID = this.FormSelect[selVal - 1].l_PARID2;
          break;
        case 3:
          this.Form.l_PARID = this.FormSelect[selVal - 1].l_PARID3;
          break;
        case 4:
          this.Form.l_PARID = this.FormSelect[selVal - 1].l_PARID4;
          break;

        default:
          this.Form.l_PARID = "";
          break;
      }
      //this.getParentData(this.selVal );
      this.getZQDataByJB(selVal);
    },
    //选择行政级别
    currentJB(Value) {
