          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          let data = res.data.result;
          var i = res.data.l_ZQJB;

          if (i == 2) {
            this.parentoptions_shi = [];
            this.FormSelect[2].l_PARID2 = "";
            this.FormSelect[3].l_PARID3 = "";
            this.FormSelect[4].l_PARID4 = "";
            this.parentoptions_shi = data;
            this.Form.l_ZQJB = "";
            this.Form.c_ZQCODE = "";
