            this.FormSelect[4].l_PARID4 = "";
            this.parentoptions_xian = data;
            this.Form.l_ZQJB = "";
            this.Form.c_ZQCODE = "";
            this.options = this.options3;
          } else if (i == 4) {
            this.parentoptions_xiang = [];
            this.FormSelect[4].l_PARID4 = "";
            this.parentoptions_xiang = data;
            this.Form.c_ZQCODE = "";
            this.Form.l_ZQJB = "";
            this.options = this.options4;
          } else {
            this.Form.l_ZQJB = "";
            this.Form.c_ZQCODE = "";
            this.options = this.options5;
