          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取初始化数据
    GetZQData() {
      return this.$axios
        .post(this.$config.serverUrl + "api/SystemSet/GetZQData", {
          headers: {
            "Content-Type": "application/json"
          }
        })
        .then(res => {
          if (res.status) {
            var tree = [];
            var selects = [];
            this.dataprop=[];
            let data = res.data;
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
              var json = {
                name: data[i].c_ZQNAME,
                code: data[i].c_ZQCODE,
                children: []
              };
              var jsonselect = {
                value: data[i].c_ZQCODE,
                label: data[i].c_ZQNAME
              };
              this.dataprop.push(json)
              tree.push(json);
              selects.push(jsonselect);
              this.parentoptions = [];
              this.parentoptions = selects;
            }
            this.dic = tree;
            for(var i=0;i<data.length;i++){
                if(data[i].l_ZQJB=="1"){
                  data[i].zqjb="省"
                }
            }
            this.alltableData = data;
            this.total = data.length;
            this.tableData = this.alltableData.slice(0, 10);
            //return resolve(tree);
          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取省级以下数据
    GetZQDataByCode(code, datatree) {
      this.$axios
        .post(
          this.$config.serverUrl + "api/SystemSet/GetZQDataByCode",
          JSON.stringify(code),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          if (res.status) {
            var tree = [];
            let data = res.data;
            datatree.children=[];
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
              var json = {
                name: data[i].c_ZQNAME,
                code: data[i].c_ZQCODE,
                children: []
              };
              tree.push(json);
              
              datatree.children.push(json);
               if(data[i].l_ZQJB=="1"){
                  data[i].zqjb="省"
                }else if(data[i].l_ZQJB=="2"){
                  data[i].zqjb="市"
                }else if(data[i].l_ZQJB=="3"){
                  data[i].zqjb="县"
                }else if(data[i].l_ZQJB=="4"){
                  data[i].zqjb="乡镇"
                }else if(data[i].l_ZQJB=="5"){
                  data[i].zqjb="村"
                }
            }
            if (data[0].l_ZQJB == 2) {
              this.dic_shi = tree;
            } else if (data[0].l_ZQJB == 3) {
              this.dic_xian = tree;
            } else if (data[0].l_ZQJB == 4) {
              this.dic_xiang = tree;
            } else {
              this.dic_cun = tree;
            }
            this.alltableData = data;
            this.total = data.length;
            this.tableData = this.alltableData.slice(0, 10);
            this.currentPage1 = 1;
          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 根据级别数据
    getZQDataByJB(selVal) {
      this.$axios
        .post(
          this.$config.serverUrl + "api/SystemSet/GetZQDataByJB",
