                    }else if(data[i].l_ZQJB=="3"){
                      data[i].zqjb="县"
                    }else if(data[i].l_ZQJB=="4"){
                      data[i].zqjb="乡镇"
                    }else if(data[i].l_ZQJB=="5"){
                      data[i].zqjb="村"
                    }
                  this.alltableData.push(data[i]);
                  }else{
                    treeJson.push(data[i]);
                  }  
              }
              this.total = this.alltableData.length;
              this.tableData = this.alltableData.slice(0, 9);
                    let render = this.getJsonTree(treeJson);
                      this.dataprop=[];
                      this.dataprop=render;  
                } else {
                  this.$message.error(res.status);
                }
              })
              .catch(error => {
                console.log(error);
              });
       }

