    // 保存编辑
    saveEdit() {
      this.SaveEditData(this.form);
      this.$message.success(`修改第 ${this.idx + 1} 行成功`);
      this.alltableData[this.form.index].c_ZQNAME = this.form.c_ZQNAME;
      this.alltableData[this.form.index].c_ZQCODE = this.form.c_ZQCODE;
      this.editVisible = false;
      var val = Math.floor(this.form.index / 10) + 1;
      this.handleCurrentChange(val);
      //
      this.$refs.tree.getNode(
        this.form.c_ZQCODE
      ).data.name = this.form.c_ZQNAME;
    },
    // 保存新增
    saveAdd() {
      this.SaveAddData(this.Form).then(res => {
        var json = { name: this.Form.c_ZQNAME };
        var data = [];
        if (this.Form.l_ZQJB == 2) {
          data = this.dic;
        } else if (this.Form.l_ZQJB == 3) {
          data = this.dic_shi;
        } else if (this.Form.l_ZQJB == 4) {
          data = this.dic_xian;
        } else {
          data = this.dic_xiang;
        }
        if (this.Form.l_ZQJB == 1) {
          this.dataprop.push(json);
          this.GetAddZQData();
        } else {
          var code = this.Form.l_PARID;
          for (var i = 0; i < data.length; i++) {
            if (data[i].code == code) {
              data[i].children.push(json);
            }
          }
          this.GetZQDataByParid(code);
        }
        this.Form.c_ZQNAME = "";
        this.Form.c_ZQCODE = "";
        this.Form.l_PARID = "";
        this.Form.l_ZQJB = "";
      });
      this.$message.success(`新增第 ${this.idx + 1} 行成功`);

      this.dialogVisible = false;
    },
    // 确定删除
    deleteRow() {
      this.delectEditData(this.form.id).then(res => {
        var code = this.form.l_PARID;
        var n = this.form.index;
        var index = Math.ceil(n / 10);
        if (this.form.l_ZQJB == 1) {
          this.GetDelectQData(index);
        } else {
          this.GetdelectDataByParid(code, index);
        }
      });
      this.$message.success("删除成功");
      console.log(this.form.c_ZQCODE);
      this.$refs.tree.remove(this.form.c_ZQCODE);
      this.delVisible = false;
      //
    },
    //
    handleNodeClick(data, node) {
      if (node.level > 0) {
        var code = node.data.code;
        var datatree=data;
        this.GetZQDataByCode(code, datatree);
      }
    },
  }
};
</script>

