</template>

<script>
	import common from "../common/js/common.js"
	import uploadFile from "../common/vue/Upload"
	import xiaobanList from "../common/vue/XiaoBanList.vue"
    export default {
        name: 'ForestAccept',
        components:{
           uploadFile:uploadFile,
           xiaobanList,
        },
        data() {
            return {
                tableData: [],
                loading:true,
                currentPage: 1,
                pageSize:5,
                totalPage:0,
                cantrolType:"项目验收信息",
                activeName:"first",
                multipleSelection: [],
                selectProState: [],
                check:true,
                drawer:false,
                titleMsg:"",
                styles: {
                    height: 'calc(100% - 55px)',
                    overflow: 'auto',
                    fontSize:"16px",
                    paddingBottom: '53px',
                    position: 'static'
                },
                acceptData:{
                	sjmj:"",
                    hsmj:"",
                    hgmj:"",
                    mjhsqk:"",
                    zyzl:"",
                    zysjzl:"",
                    zzglqk:"",
                    xxdajs:"",
                    zhpf:"",
                    sfhg:"true",
                    ysyj:"",
                },
                formData: {
                    xmmc: '',
                    xbh:"",
                    xmlb: '',
                    state:"",
                    gclb: '',
                    yzlfs: '',
                    gcssnd:"",
                    tzwjwh:"",
                    xmztz:"",
                    jsnr:"",
                    jsgm:"",
                    xmzt: '',
                    xmfzr:'',
                    xmsjdw: '',
                    xmsjrq:'',
                    bz: '',   
                    
                },
                acceptDataValidate:{
                	sjmj: [
                        { required: true, message: '不能为空', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                    
                    hsmj: [
                        { required: true, message: '不能为空', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                    hgmj: [
                        { required: true, message: '不能为空', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                    
                    mjhsqk: [
                        { required: true, message: '不能为空', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                },
                projectId:"",
                keyword: '',
                rowId:"",
                idx: -1,  //选中页的行数
                
            }
        },
        computed: {
        	 turnToAccept() {
        	 	if(this.$store.state.pro.name && this.$route.params.ticket=="ForestAccept"){
        	 		this.keyword  = this.$store.state.pro.name;
        	 		this.$axios.post(this.$config.serverUrl+"api/FL_YZL_JCYS/GetFL_YZL_JCYS", 
        	 		
	                JSON.stringify({
	                	    keyword: $.trim(this.keyword) ,
	                	    states:[4,5],
	                	    token:sessionStorage.getItem("token"),
		                	pageQueryInfo:{
				                	pageSize: this.pageSize,
				    				pageIndex: this.currentPage
		                	},
	                	}),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	                	console.log(res);
	                    this.tableData = res.data.resultValue;
	                    this.totalPage=res.data.count;
	                    this.loading= false;
	                    this.$refs.multipleTable.setCurrentRow(this.tableData[0]);

	                }).catch(error=>{
	                	this.$message.error(error);
	                	})
        	 	}
			    return this.$store.state.pro;
			  },
        },
        watch:{
				 turnToAccept(val) {
				     return this.$store.state.pro;
				  }
		},
		created(){
			this.getData();
		},
        methods: {
        	getState(state){
        		let str ="";
        		switch(state){
        			case "审核中":
        			str =   "primary";
        			break;
        			break;
        			case "待验收":
        			str =   "primary";
        			break;
        			case "待核查":
        			str =   "success";
        			break;
        			case "已退回":
        			str =   "error";
        			break;
        			default:
        			str =   "primary";
        		}
        		return str;
        	},
        	handleSizeChange(val){
        		this.pageSize = val ;
        		this.getData();
        	},
            // 分页导航
            handleCurrentChange(val) {	
                this.currentPage = val;
                this.getData();
            },
             toSubcompartment(data,type){
             	
             	this.drawer = false;
            	this.$store.commit("setXmbh",{
            		xmbh:data? data.xmbh : this.XMBH,
            		ToState:5,
            	});
            	this.$router.push({
            						name:"SubcompartmentWorkSpace",
            						params:{
            							type,
            							xmbh: data ? data.xmbh : this.XMBH,
            							ToState:5,
            							ToName:"验收后待核查"}
            						});
             	
            	
            },
            refresh(){  //重置
            	this.$store.commit("setPro",{});
            	this.keyword = "";
            	this.currentPage= 1;
                this.pageSize=5;
                this.getData();
            },
            // 获取 easy-mock 的模拟数据
            getData() {
              return   this.$axios.post(this.$config.serverUrl+"api/FL_YZL_JCYS/GetFL_YZL_JCYS", 
                JSON.stringify({
                	    keyword:$.trim(this.keyword),
                 	    states:[4,5],
                	    token:sessionStorage.getItem("token"),
	                	pageQueryInfo:{
			                	pageSize: this.pageSize,
			    				pageIndex: this.currentPage
	                	},
                	}),
                	{headers: {'Content-Type': 'application/json'}}
                ).then((res) => {
                	console.log(res);
                    this.tableData = res.data.resultValue;
                    this.totalPage=res.data.count;
                    this.loading= false;
                }).catch(error=>{
                	this.$message.error(error);
                	})
            },
            toNext(row,to){
            	if(row && row.state_Name){  //toRow
            		
            		if(row.state_Name == "待验收"){  //验收操作
            			this.formData = row;
		            	if(to == "pass"){ //通过
		            		this.formData.state = 5;
		            		this.titleMsg =  "是否确认项目信息并验收通过？";
		            		
		            	}
		            	//this.submitVisible = true;
		            	
		            	this.$confirm(this.titleMsg, '提示', {
					          confirmButtonText: '确定',
					          cancelButtonText: '取消',
					          type: 'warning'
					        }).then(() => {
					           
					           //1,验证是否有必填项
					           //2，是的话更改对应项目状态
					        	this.saveAccept().then(()=>{
					           				this.submitFrom('submit');
					           			}).catch(()=>{
					           				this.$message.info("验收失败"); 
					           	})
					           
					        }).catch(() => {
					             this.$message.info("已取消");        
					        });
	            		
	            	}else if(row.state_Name == "待核查"){  //去核查操作
	            		//监听变化
	            		this.$store.commit("setPro",{
	            					proID:row.id,
	            					name:row.xmmc,
	            			});
	            		//	this.$router.replace("/icon");
	            		this.$router.push({name:'ForestCheck',params:{
	            			proID:row.id,
	            			name:row.xmmc,
	            			ticket:'ForestCheck'
	            		}});
	            		
	            	}
	            	
            	}else{   //toFormData 
            		
            		if(to == "pass"){ //通过
		            		this.formData.state = 5;
		            		this.titleMsg = "是否确认项目信息并验收通过？";
		            //		this.submitVisible = true;
		            		this.$confirm(this.titleMsg, '提示', {
					          confirmButtonText: '确定',
					          cancelButtonText: '取消',
					          type: 'warning'
					        }).then(() => {
					           
					          		//1,验证是否有必填项
						           //2，是的话更改对应项目状态
						           this.saveAccept().then(()=>{
					           				this.submitFrom('submit');
					           			}).catch(()=>{
					           				this.$message.info("验收失败"); 
					           		})
					           
					        }).catch(() => {
					             this.$message.info("已取消");        
					        });
		            }else{
		            		this.$message.error("数据错误");
		            }
            	}
            },
            search() {
                getData();
            },
			formatter(row,ele){
 				return common.formatDate(row.createDate);
			},
            handleEdit(index, row) {
            	console.log(row.accept?row.accept.sjmj:row.accept);
            	//深拷贝，，表单与table解绑，，，，主要是解决重置表单后，表格的数据跟着被清空
            	if(this.tableData[this.idx]){
            		let obj = {
            			project:JSON.parse(JSON.stringify(this.formData)),
            			accept:JSON.parse(JSON.stringify(this.acceptData))
            		}
            		this.tableData[this.idx] = obj;
            	}
            	
            	
                this.idx = index;
                //重置表单
                this.$refs["Form"].resetFields();
                this.$refs["acceptForm"].resetFields();
                let  item = row.project;
                row.accept ? this.acceptData = row.accept:this.acceptData={};
               
                item.createDate = common.formatDate(item.createDate);
                item.updateDate = common.formatDate(item.updateDate);
                item.type = item.type + "";
                item.method = item.method + "";
                this.formData = item;
                this.cantrolType = "项目验收信息";
                this.drawer = true;
                
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            saveAccept(name){

	           	let _this = this;
	            let validate =function(resolve, reject){
	            		_this.$refs["acceptForm"].validate((valid) => {
	            			if(valid){
	            				resolve();
	            			}else{
	            				reject();
	            			}
	            		})
	            }
	            let t1 = new Promise(validate);
            
            	t1.then(()=>{
            					let value = {};
		                    	value = this.acceptData;
		                    	!value.xmbh ? value.xmbh =this.formData.xmbh:null;
			                    value.token = this.$store.state.token;
			              //  	console.log(JSON.stringify(value));
			                		
			                	if(value.id){  //有ID为修改，用axios put 方法
			                		
			                		this.$axios.put(this.$config.serverUrl+"/api/FL_YZL_JCYS/PutFL_YZL_JCYS/"+value.id, 
						                JSON.stringify(value),
						                	{headers: {'Content-Type': 'application/json'}}
						               ).then((res) => {
						 //              		console.log(res);
						                	this.$message.success("保存成功");
						                }).catch(error=>{
						                	
			                				this.$message.error(error);
			                			})
			                	}else{    //没有ID为新增，post方法
			                		this.$axios.post(this.$config.serverUrl+"api/FL_YZL_JCYS/PostFL_YZL_JCYS", 
						                JSON.stringify(value),
						                	{headers: {'Content-Type': 'application/json'}}
						                ).then((res) => {
						 //               	console.log(res);
						                	this.getData();
						                	this.$message.success("保存成功");
						                }).catch(error=>{
			                				this.$message.error(error);
			                			})
			                	}	
	            });
	            
	            t1.catch(()=>{
	            	this.$Message.error('请正确填写验收信息');
	            })
            	
             	return	t1;
            },
            submitFrom(name){
            	
            	if(name == "submit"){
            		 
            		this.$refs["Form"].validate((valid) => {
		                    if (valid) {
		                    	
		                    	let value = this.formData;
			                    	value.token = this.$store.state.token;
			                		!value.id ? value.id =this.rowId:null;

		                    	this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/UpdateYZL_ZLXM", 
					                JSON.stringify(value),
					                	{headers: {'Content-Type': 'application/json'}}
					                ).then((res) => {
					                	
					                	this.$message.success("操作成功");
					                	this.drawer = false;
					                	this.getData();
					                }).catch(error=>{
					                
		                				this.$message.error(error);
		                			})
		                
		                    } else {
		                        this.$Message.error('请正确填写');
		                    }
		                })
            		
            		
            	}else{
            		this.$Message.error('数据错误');
            	}
            
            }
            
        }
    }

</script>

<style scoped>
	@import "../common/css/proCommon.css";
</style>



