<template>
    <div class="table">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-lx-cascades"></i>{{this.$route.meta.title}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
        	<div class="table">
        		 <div class="handle-box">
	                <el-button  type="primary" icon="el-icon-delete" class="handle-del mr10" @click="delAll">批量删除</el-button>
	                <el-input v-model="keyword" placeholder="搜索关键词" class="handle-input mr10"></el-input>
	                <el-button type="primary" icon="el-icon-search" @click="getData">搜索</el-button>
	                <el-button type="primary" icon="el-icon-plus" @click="addData">添加</el-button>
	                <el-button class="rightRefresh" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
	            </div>
	            <el-table 
	            	v-loading="loading"
	            	:data="tableData" 
	            	highlight-current-row
	            	border 
	            	class="table" 
	            	ref="multipleTable" 
	            	@selection-change="handleSelectionChange">
	                <el-table-column 
	                	type="selection" 
	                	width="55" 
	                	:selectable = "selectable"
	                	align="center"></el-table-column>
	                <el-table-column prop="createDate" label="日期" 
	                	:formatter="formatter"
	                	 align="center" width="160">
	                </el-table-column>
	                <el-table-column prop="xmbh" label="项目编号" align="center" width="130">
	                </el-table-column>
	                <el-table-column prop="xmmc" label="项目名称"  align="center" >
	                </el-table-column>
	                <el-table-column
				      prop="state_Name"
				      align="center"
				      label="状态"
				      width="120"
					>
				      <template slot-scope="scope">
				        <Tag
				          :color="getState(scope.row.state_Name)"
				          type="dot"
				          fade>{{scope.row.state_Name}}</Tag>
				      </template>
				    </el-table-column>
	               <!--本页表格数据筛选
	               	<el-table-column
				      prop="state"
				      align="center"
				      label="进度"
				      width="100"
				      :filters="[{ text: '待审核', value: '待审核' }, { text: '待提交', value: '待提交' }]"
				      :filter-method="filterTag"
				      filter-placement="bottom-end">
				      <template slot-scope="scope">
				        <el-tag
				          :type="scope.row.state === '待审核' ? 'primary' : 'success'"
				          disable-transitions>{{scope.row.state}}</el-tag>
				      </template>
				    </el-table-column>-->
	                <el-table-column label="小班数" width="180" align="center">
	                    <template slot-scope="scope">
	                        <el-tooltip 
	                        	class="item" 
	                        	effect="dark" 
	                        	placement="top"
	                        	>
	                        	<div slot="content" v-if="scope.row.state_Name =='未提交' || scope.row.state_Name =='已退回'">{{scope.row.currentStatesCount || 0}}个小班待提交，总共{{scope.row.totalCount || 0}}个小班</div>
						       <el-button
						       	:disabled="(scope.row.state_Name =='未提交' || scope.row.state_Name =='已退回') ? null :'disabled' "
						       	 @click="toSubcompartment(scope.row,'watch')">{{scope.row.currentStatesCount || 0}}/{{scope.row.totalCount || 0}}</el-button>
						    </el-tooltip>
	                    </template>
	                </el-table-column>
	                <el-table-column label="操作" align="center" width="300">
	                    <template slot-scope="scope">
	                        <el-button  :disabled="(scope.row.state_Name =='未提交' || scope.row.state_Name =='已退回') ? null :'disabled' "  
	                        	type="text" 
	                        	icon="el-icon-edit" 
	                        	@click="handleEdit(scope.$index, scope.row)">编辑</el-button>
	                        <el-button 
	                        	:disabled="(scope.row.state_Name =='未提交' || scope.row.state_Name =='已退回') ? null :'disabled' "  
	                        	type="text" 
	                        	size="small"
	                        	icon="el-icon-delete" 
	                        	:class="(scope.row.state_Name =='未提交' || scope.row.state_Name =='已退回') ? 'red' : null " 
	                        	@click="handleDelete(scope.$index, scope.row)">删除</el-button>
	                        <el-button 
	                        	type="text" 
	                        	:icon="(scope.row.state_Name =='未提交' || scope.row.state_Name =='已退回') ? 'fa fa-check-circle-o': 'fa fa-hand-o-right'" 
	                        	v-show="check" 
	                        	@click="toNext(scope.row)">&nbsp;{{(scope.row.state_Name =='未提交' || scope.row.state_Name =='已退回')? "提交" : "审核"}}</el-button>
	                    </template>
	                </el-table-column>
	            </el-table>
	            <div class="pagination">
					 <el-pagination
				      @size-change="handleSizeChange"
				      @current-change="handleCurrentChange"
				      :current-page="currentPage"
				      :page-sizes="[5, 10, 15, 20]"
				      :page-size="pageSize"
				      layout="total, sizes, prev, pager, next, jumper"
				      :total="totalPage">
				    </el-pagination>
	            </div>
        	</div>
        </div>
		

		<!--抽屉框，查看-->
		<Drawer
            :title="cantrolType"
            v-model="drawer"
            width="720"
            :mask-closable="false"
            :styles="styles"
        >
	    	<div id="basicmsg">
		        	<Tabs  v-model="activeName" on-click="handleClick">
					    <TabPane  label="项目基本信息" name="first">
					    	 <el-form 
					    	 	class = "formDiv"
					    	 	ref="Form"
					    	 	size = "small"
					    	 	:rules="formDatatValidate" 
					    	 	:model="formData">
				                <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="项目名称:" label-position="top" prop="xmmc">
				                           <!-- <el-input v-model="formData.name" placeholder="please enter user name" />-->
				                            <el-input v-model="formData.xmmc" placeholder="请填写"></el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目编号:" label-position="top" prop="xmbh">
				                            <el-input v-model="formData.xmbh" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                <el-row :gutter="32">
				                	
				                	<el-col :span="12">
				                        <el-form-item label="造林计划:" label-position="top" prop="jH_Id">
				                            <el-select 
				                            	v-model="formData.jH_Id" 
				                            	class = "selectDiv"
				                            	placeholder="请选择">
				                           		 <el-option
											      v-for="item in JH_IDOptions"
											      :key="item.id"
											      :label="item.jiHuaMingCheng"
											      :value="item.id">
											    </el-option>
				                            </el-select>
				                        </el-form-item>
				                    </el-col>
				                	
				                    <el-col :span="12">
				                        <el-form-item label="项目类别:" label-position="top">
				                            <el-input v-model="formData.xmlb" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>

				                </el-row>
				                 <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="投资文件文号:" label-position="top">
				                            <el-input v-model="formData.tzwjwh" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目总投资:" label-position="top">
				                            <el-input v-model="formData.xmztz" placeholder="请填写">
				                            	<template slot="append">万元</template>
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                <el-row :gutter="32">
				                	<el-col :span="12">
				                        <el-form-item label="工程类别:" label-position="top">
				                            <el-select 
				                            	v-model="formData.gclb" 
				                            	class = "selectDiv"
				                            	placeholder="请选择">
				                           		<el-option
											      v-for="item in projectType"
											      :key="item.code"
											      :label="item.name"
											      :value="item.code">
											    </el-option>
				                            </el-select>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="营造林方式 :" label-position="top">
				                            <el-select 
				                            	v-model="formData.yzlfs" 
				                            	class = "selectDiv"
				                            	placeholder="请选择">
				                                <el-option
											      v-for="item in afforestationType"
											      :key="item.code"
											      :label="item.name"
											      :value="item.code">
											    </el-option>
				                            </el-select>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                <el-row :gutter="32">
				                    <el-col :span="12">
				                        <el-form-item label="建设内容:" label-position="top">
				                            <el-input v-model="formData.jsnr" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="建设规模:" label-position="top">
				                            <el-input v-model="formData.jsgm" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                       <el-form-item label="项目实施主体:" label-position="top">
				                            <el-input v-model="formData.xmzt" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="项目负责人:" label-position="top">
				                            <el-input v-model="formData.xmfzr" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                 </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                       <el-form-item label="设计单位:" label-position="top">
				                            <el-input v-model="formData.xmsjdw" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    <el-col :span="12">
				                        <el-form-item label="设计日期:" label-position="top">
				                            <el-input v-model="formData.xmsjrq" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    
				                 </el-row>
				                 <el-row :gutter="32">
				                 	<el-col :span="12">
				                        <el-form-item label="设计证书等级:" label-position="top">
				                            <el-input v-model="formData.SJZSDJ" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                 	<el-col :span="12">
				                        <el-form-item label="设计单位负责人:" label-position="top">
				                            <el-input v-model="formData.SJDWFZR" placeholder="请填写">
				                            
				                            </el-input>
				                        </el-form-item>
				                    </el-col>
				                    
				                 </el-row>
				                <el-form-item label="描述" label-position="top">
				                    <el-input type="textarea" v-model="formData.bz" :rows="4" placeholder="项目描述" />
				                </el-form-item>
				                <el-row :gutter="32">
				                	<el-col :span="24">
				                		<Button  type="primary" @click="submitFrom('Form')">保存 </Button >
				                	</el-col>
				                </el-row>
				            </el-form>
		
					    </TabPane>
					    <TabPane label="项目材料" name="second">
					    	<upload-file v-bind:XMBH="formData.xmbh" v-bind:SCWJZL="formData.SCWJZL" ></upload-file>
					    	
					    </TabPane>
					    <TabPane label="造林小班" name="third">
					    	<xiaoban-list v-bind:XMBH="formData.xmbh" v-bind:state="formData.state" :drawer.sync="drawer" ></xiaoban-list>
					    </TabPane>
					    <TabPane label="项目经费" name="fourth">项目经费</TabPane>
					 </Tabs >
					 <span>版本号:<span class="blueText">{{formData.version || "1.0.0"}}</span>；
					 	<span class="blueText">{{formData.createUser_Name}}</span>于
					 	{{formData.createDate }}&nbsp;创建，
					 	<span class="blueText">{{formData.updateUser_Name}}</span>{{ formData.updateDate }}&nbsp;修改</span>
					 <div class="demo-drawer-footer">
					 	<Button type="primary" @click="toNext">提交</Button>
				        <Button style="margin-left: 8px" @click="drawer = false">关闭</Button>
				    </div>
	    	</div>
        </Drawer>    
        
        <!--编辑项目基本信息框，查看-->
			<Modal
		        v-model="baseMsg"
		        title="填写项目基本信息"
		        >
		         <el-form ref="baseForm" :model="formData" :rules="formDatatValidate" label-width="80px">
			        <el-form-item label="项目名称" prop="xmmc">
			            <el-input v-model="formData.xmmc" placeholder="项目名称"></el-input>
			        </el-form-item>
			        <el-form-item label="项目编号" prop="xmbh">
			            <el-input v-model="formData.xmbh" placeholder="项目编号"></el-input>
			        </el-form-item>
			        <el-form-item label="所属计划" prop="jH_Id">
			            <!--<el-input v-model="formData.jH_Id" placeholder="项目编号"></el-input>
			            -->
			            
			             <el-select
						    v-model="formData.jH_Id"
						    class = "selectDiv"
						    :loading="loading">
						    <el-option
						      v-for="item in JH_IDOptions"
						      :key="item.id"
						      :label="item.jiHuaMingCheng"
						      :value="item.id">
						    </el-option>
					 	 </el-select>
			            
			        </el-form-item>
			     </el-form>
	            <span slot="footer" >
	                <Button @click="baseMsg = false">取 消</Button >
	                <Button  type="primary" @click="submitBase('baseForm')">确 定</Button >
	            </span>
		    </Modal>
    </div>
</template>

<script>
	import common from "../common/js/common.js"
	import xiaobanList from "../common/vue/XiaoBanList.vue"
	import uploadFile from "../common/vue/Upload"
    export default {
        name: 'ForestDesign',
        components:{
           uploadFile:uploadFile,
           xiaobanList,
        },
        data() {
            return {
                tableData: [],
                loading:true,
                currentPage: 1,
                pageSize:5,
                totalPage:0,
                baseMsg:false,
                cantrolType:"添加项目信息",
                activeName:"first",
                multipleSelection: [],  //多选表格，多选项目信息 删除用
                selectProState: [],    //下拉列表
                projectType: [],       //下拉列表
                afforestationType: [],
                check:true,
                drawer:false,
                styles: {
                    height: 'calc(100% - 55px)',
                    overflow: 'auto',
                    fontSize:"16px",
                    paddingBottom: '53px',
                    position: 'static'
                },
                XMBH:null,
                formData: {
                    xmmc: '',
                    xmbh:"",
                    jH_Id:null,
                    xmlb: '',
                    state:"",
                    gclb: '',
                    yzlfs: '',
                    gcssnd:"",
                    tzwjwh:"",
                    xmztz:"",
                    jsnr:"",
                    jsgm:"",
                    xmzt: '',
                    xmfzr:'',
                    xmsjdw: '',
                    xmsjrq:'',
                    bz: ''
                },
                projectId:"",
                JH_IDOptions:[],
                keyword: '',
                rowId:"",
                idx: -1,  //选中页的行数
                formDatatValidate:{
                	xmmc: [
                        { required: true, message: '请输入项目名称', trigger: 'blur' }
                    ],
                    xmbh: [
                        { required: true, message: '请输入项目编号', trigger: 'blur' },
                    ],
                    jH_Id: [
                        { required: true, message: '请选择工程所属计划', trigger: 'blur' },
                    ],
                }
                
            }
        },
        created() {
        //	this.getButton();  //权限设置
            this.getData();
            this.getDic();
        },
        computed: {
        	 turnToDesign() {
        	 	if(this.$store.state.pro.name && this.$route.params.ticket=="ForestDesign"){
        	 		console.log(this.$route);
        	 		this.keyword = this.$store.state.pro.name;
        	 		this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/GetYZL_ZLXM", 
	                JSON.stringify({
	                	    keyword:this.keyword,
	                	    isSubcompartmentInfo: true,
	                	    states:[1,2,3],
	                	    token:sessionStorage.getItem("token"),
		                	pageQueryInfo:{
				                	pageSize: this.pageSize,
				    				pageIndex: this.currentPage
		                	},
	                	}),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	                	console.log(res);
	                    this.tableData = res.data.resultValue;
	                    this.totalPage=res.data.count;
	                    this.loading= false;
	                    this.$refs.multipleTable.setCurrentRow(this.tableData[0]);
	                    
	                   
	                }).catch(error=>{
	                	this.$message.error(error);
	                	})
	                
	                return this.$store.state.pro.proID;
        	 	}
			   
			  },
        },
        watch:{
				 turnToDesign(val) {
				     return this.$store.state.pro.proID;
				  }
		},
        methods: {
        	getState(state){
        		let str ="";
        		switch(state){
        			
        			case "未提交":
        			str =   "primary";
        			break;
        			case "审核中":
        			str =   "warning";
        			break;
        			case "待验收":
        			str =   "#87e8de";
        			break;
        			case "待核查":
        			str =   "success";
        			break;
        			case "已退回":
        			str =   "error";
        			break;
        			default:
        			str =   "primary";
        		}
        		return str;
        	},
        	handleSizeChange(val){
        		this.pageSize = val ;
        		this.getData();
        	},
            // 分页导航
            handleCurrentChange(val) {	
                this.currentPage = val;
                this.getData();
            },
            getButton(){
            	const value = {
            		token:sessionStorage.getItem("token"),
            		moduleId:this.$route.meta.id,
            	};
            	console.log(JSON.stringify(value));
            	this.$axios.post(this.$config.serverUrl+"api/Button/GetButtons", 
	                JSON.stringify(value),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	                	console.log(res);
	                }).catch(error=>{
	                	this.$message.error(error);
	                	})
            },
            addData(){
            	this.baseMsg = true ; 
            	this.formData = {};
            	this.$refs["baseForm"].resetFields();
            },
            getDic(){

	           this.$store.dispatch("getDics",["ProjectState","PROJECTTYPE","AFFORESTATIONTYPE"]).then(data=>{
	           		for(let obj of data){
							switch(obj.type){
								case "ProjectState":
								this.selectProState  = obj.data;
								break;
								case "PROJECTTYPE":
								this.projectType  = obj.data;
								break;
								case "AFFORESTATIONTYPE":
								this.afforestationType  = obj.data;
								break;
							}
						}
	           });
	                
	            this.$axios.post(this.$config.serverUrl+"api/ZaoLinJiHua/GetZljhData", 
	                JSON.stringify(window.sessionStorage.getItem('token')),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	     
	                	if(res && res.data){
	                		this.JH_IDOptions = res.data;
	                	}else{
	                		this.$message.error("数据错误");
	                	}
	                }).catch(error=>{
	                	this.$message.error(error);
	                })   
	                
            },
            refresh(){  //重置
            	this.$store.commit("setPro",{});
            	this.keyword = "";
            	this.currentPage= 1;
                this.pageSize=5;
                this.getData();
            },
            // 获取 easy-mock 的模拟数据
            getData() {
                // 开发环境使用 easy-mock 数据，正式环境使用 json 文件
                if (process.env.NODE_ENV === 'development') {  //开发环境
                    this.url = 'https://easy-mock.com/mock/5bd9623b425b8a0c39a5b280/example/getForestData';
                };
               
               return this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/GetYZL_ZLXM", 
                JSON.stringify({
                	    keyword:$.trim(this.keyword) || "",
                	    states:[1,2,3],
                	    token:sessionStorage.getItem("token"),
                	    isSubcompartmentInfo: true,
	                	pageQueryInfo:{
			                	pageSize: this.pageSize,
			    				pageIndex: this.currentPage
	                	},
                	}),
                ).then((res) => {
       
                    this.tableData = res.data.resultValue;
                    this.totalPage=res.data.count;
                    this.loading= false;
                }).catch(error=>{
                	this.$message.error(error);
                	})
            },
            toNext(row){
            	if(row.xmbh){  // row 提交

            		if(row.state_Name =='未提交' || row.state_Name =='已退回'){   //提交
            		//	this.submitVisible = true;
            			this.formData = row;
            			this.$confirm('是否确认信息并提交？', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).then(() => {
				           this.submitFrom("submit")
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: '已取消'
				          });          
				        });
	            	}else if(row.state_Name =='审核中'){
	            		 //到审批页面
	            		 //	this.$router.replace("/icon");
	            		 this.$store.commit("setPro",{
	            					proID:row.id,
	            					name:row.xmmc,
	            			});
	            		this.$router.push({
	            			name:'ForestApproval',
	            			params:{
	            					proID:row.id,
	            					name:row.xmmc,
	            					ticket:'ForestApproval',
	            				}
	            			});
	            		
	            	}
            		
            	}else{   //form 提交
            		//this.submitVisible = true;
            			this.$confirm('是否确认信息并提交？', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).then(() => {
				           this.submitFrom("submit")
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: '已取消'
				          });          
				        });
            	}
            	
            },
            search() {
                getData();
            },
            selectable(row,index){
            	if(row.state_Name =='未提交' || row.state_Name =='已退回'){
            		return true;
            	}else{
            		return false;
            	}
            },
            toSubcompartment(data,type){
            	console.log(data);
            	this.drawer = false;
            	this.$store.commit("setXmbh",{
            		xmbh:data? data.xmbh : this.XMBH,
            		ToState:3,
            	});
            	this.$router.push({
            						name:"SubcompartmentWorkSpace",
            						params:{
            							type,
            							XBkeyword:data.xbh || null,
            							xmbh: data ? data.xmbh : this.XMBH,
            							ToState:3,
            							ToName:"提交后待审批"}
            						});
            },
			formatter(row,ele){
 				return common.formatDate(row.createDate);
			},
            handleEdit(index, row) {
            	//深拷贝，，表单与table解绑，，，，主要是解决重置表单后，表格的数据跟着被清空
            	this.formData.xmbh ? this.tableData[this.idx] = JSON.parse(JSON.stringify(this.formData)):null;
            	
                this.idx = index;
                this.XMBH = row.xmbh;
                
                //重置表单
                this.$refs["Form"].resetFields();
                let  item = this.tableData[index];
                //console.log(item);
                item.createDate = common.formatDate(item.createDate);
                item.updateDate = common.formatDate(item.updateDate);
                item.gclb = item.gclb + "";
                item.yzlfs = item.yzlfs + "";
                this.formData = item;
                this.cantrolType = "修改项目信息";
                this.drawer = true;
            },
            handleDelete(index, row) {
                this.idx = index;
                this.$confirm('此操作将永久删除该项目信息, 是否继续?', '提示', {
			          confirmButtonText: '确定',
			          cancelButtonText: '取消',
			          type: 'warning'
			        }).then(() => {
						let delArr= [row.id];  
		                this.delData(delArr).then(()=>{
		            			this.getData();
		            		})
						
			        }).catch(() => {
			          this.$message({
			            type: 'info',
			            message: '已取消'
			          });          
			        });
                
                
            },
            delAll() {
            	if(this.multipleSelection.length<1){
            		this.$message.error("您未选择");
            	}else{

            		this.$confirm('此操作将永久删除选中的项目信息, 是否继续?', '提示', {
			          confirmButtonText: '确定',
			          cancelButtonText: '取消',
			          type: 'warning'
			        }).then(() => {
						let delArrAll=[];
	            		for(let i of this.multipleSelection){
	            			delArrAll.push(i.id)
	            		}
	            		this.multipleSelection = [];
					
		                this.delData(delArrAll).then(()=>{
		            			this.getData();
		            		})
						
			        }).catch(() => {
			        	this.$refs.multipleTable.clearSelection();
			          this.$message({
			            type: 'info',
			            message: '已取消'
			          });          
			        });
            		
            		
            	}
            },
            delData(arr){
            	return this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/DeleteYZL_ZLXM", 
	                JSON.stringify(arr),
	                	{headers: {'Content-Type': 'application/json'}}
	                ).then((res) => {
	                
	                	 this.$message.success('删除成功');
	                }).catch(error=>{
	                	this.$message.error(error);
	                })
            },
           
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            submitFrom(name){
            	//name 表单保存/修改（未提交），，，data 提交审核的数据
            	
            	if(name){   //保存，修改该信息
            		if(name == "Form"){ 
            			this.$refs[name].validate((valid) => {
		                    if (valid) {
		                    
		                    	let value = this.formData;
			                    	value.token = this.$store.state.token;
			                		!value.id ? value.id =this.rowId:null;
			                //	console.log(JSON.stringify(value));
		                    	this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/UpdateYZL_ZLXM", 
					                JSON.stringify(value),
					                	{headers: {'Content-Type': 'application/json'}}
					                ).then((res) => {
					                	this.getData();
					                	this.$message.success("保存成功");
					                }).catch(error=>{
					                
		                				this.$message.error(error);
		                			})
		                
		                    } else {
		                        this.$Message.error('请正确填写');
		                    }
		                })
            		}else if(name == "submit"){
            			this.$refs["Form"].validate((valid) => {
		                    if (valid) {
		                 //   	console.log( this.formData);
		                    	let value = this.formData;
			                    	value.token = this.$store.state.token;
			                    	value.state = 3; 
			                		!value.id ? value.id =this.rowId:null;
		                    	this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/UpdateYZL_ZLXM", 
					                JSON.stringify(value),
					                	{headers: {'Content-Type': 'application/json'}}
					                ).then((res) => {
					                	
					                	this.drawer = false;
					                	this.getData();
					                	this.$message.success("提交成功");
					               
					                	
					                }).catch(error=>{
					                
		                				this.$message.error(error);
		                			})
		                
		                    } else {
		                    
		                        this.$Message.error('请正确填写');
		                    }
		                })
            		}
            		
            	}else{
            		this.$message.error("数据错误");
            	}
            	
            },
            submitBase(name){
            	this.$refs[name].validate((valid) => {
                    if (valid) {
                    	this.cantrolType = "添加项目信息";
                    	this.drawer = true;
                    	this.baseMsg =false;
                    //	console.log(this.formData);
                    	this.$axios.post(this.$config.serverUrl+"api/ProjectInfo/PostYZL_ZLXM", 
			                JSON.stringify({
			                	token:window.sessionStorage.getItem('token'),
			                	xmbh:this.formData.xmbh,
			                	state:2,
			                	xmmc:this.formData.xmmc,
			                	JH_id :this.formData.jH_Id,
			                }),
			                	{headers: {'Content-Type': 'application/json'}}
			                ).then((res) => {
			                	this.getData();
			                	this.$Message.success('添加成功');
			                	this.rowId = res.data.id;
			       
			                }).catch(error=>{
                				this.$message.error(error);
                			})
                    	
                    } else {
                        this.$Message.error('请正确填写');
                    }
                })
            },
            handleClick(tab, event) {
		   //     console.log(tab, event);
		      }
            
        }
    }

</script>

<style scoped>
	@import "../common/css/proCommon.css";
</style>



