    querySearchAsync(queryString, callback) {
      var restaurants = this.restaurants;
      // 如果参数都没变化，则使用缓存数据，避免请求沉积 
            let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
            if(results.length>0){
                callback(results);
            }else {
              this.restaurants=[];
          //调用的后台接口
          this.$axios
            .post(
              this.$config.serverUrl + "api/YZL_ZLMS/GetMSMCData",
              JSON.stringify(queryString),
              {
                headers: {
                  "Content-Type": "application/json"
                }
              }
            )
            .then(res => {
              let data = res.data;
                if(data=="没有数据"){
                     var json = {
                     value: "没有数据",
                  };
                  this.restaurants.push(json);
                  callback(this.restaurants);
                }
              for (var i = 0; i < data.length; i++) {
                  data[i].index = i;
                  var json = {
                    value: data[i].msmc,
                  };
                  this.restaurants.push(json);
                  callback(this.restaurants);
                }
            })
            .catch(error => {
              console.log(error);
               if(error=="没有数据"){
                     var json = {
                     value: "没有数据",
                  };
                  this.restaurants.push(json);
                  callback(this.restaurants);
                }
            });
          
        } 
      },
    // 获取造林模式数据
    GetData() {
      this.$axios
        .post(this.$config.serverUrl + "api/YZL_ZLMS/GetData", {
          headers: {
            "Content-Type": "application/json"
          }
        })
        .then(res => {
          let data = res.data;
          this.alltableData = [];
          for (var i = 0; i < data.length; i++) {
            data[i].data.index = i;
            data[i].data.xuhao = i+1;
            if(data[i].data.dxts==""){
               data[i].data.image="https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar";
            }else{
              if(data[i].path[0]==null){
                data[i].data.image="https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar";
              }else{
                data[i].data.image =  this.$config.serverUrl+data[i].path[0].path || this.$config.serverUrl+data[i].path;
              }
            }
            this.alltableData.push(data[i].data);
          }
          this.total = data.length;
          this.tableData = this.alltableData.slice(0, 10);
          this.currentPage1 = 1;
        })
        .catch(error => {
          console.log(error);
        });
    },
     // 获取树种
    GetSZData() {
      var types=["TREE","LANDPREPARATION","SITEPREPARATION","MIXED","PLANTING","LDLX","LZFL"];
      this.$store.dispatch("getDics",types).then(data=>{
	            	for(var i=0;i<data.length;i++){
		              if(data[i].type=="TREE"){
		                this.ghszoptions =data[i].data;
		              }else if(data[i].type=="LANDPREPARATION"){
		                this.zdfsoptions =data[i].data;
		              }else if(data[i].type=="SITEPREPARATION"){
		                this.zdplfsoptions =data[i].data;
		              }else if(data[i].type=="MIXED"){
		                this.hjfsoptions =data[i].data;
		              }else if(data[i].type=="PLANTING"){
		                this.zhffsoptions =data[i].data;
		              }
		              else if(data[i].type=="LDLX"){
		                this.ldlxoptions =data[i].data;
		              }
		              else if(data[i].type=="LZFL"){
		                this.lzoptions =data[i].data;
		              }
		            }
	            })
    },
