    // 保存新增
    saveAdd(form) {
      this.$refs.upload.clearFiles();
      this.form.Token = sessionStorage.getItem("token");
      //表单验证
      this.$refs[form].validate((valid) => {
          if (!valid) {
            console.log('error submit!!');
            return false;
          } else {
                var hjbl=0;
                for (var i=0;i<this.domains.length;i++){
                        hjbl+=+this.domains[i].hjbl;
                      if(this.domains[i].zlmd==""||this.domains[i].zlmd<0){
                        alert("造林密度不能为空且不能为负");
                          return false;
                      }else if(this.domains[i].hjbl==""||Number(this.domains[i].hjbl)<0){
                        alert("混交比例不能为空且不能为负");
                          return false;
                      }
                    }
                 if(hjbl!=100){
                   alert("混交比例之和为100%");
                          return false;
                 }
                 this.SaveAddData(this.form).then(res => {
                  this.$axios
                    .post(this.$config.serverUrl + "api/YZL_ZLMS/GetData", {
                      headers: {
                        "Content-Type": "application/json"
                      }
                    })
                    .then(res => {
                      let data = res.data;
                      this.mS_Id = Math.max.apply(
                        Math,
                        data.map(function(o) {
                          return o.data.id;
                        })
                      );
                      for (var i=0;i<this.domains.length;i++){
                      this.domains[i].Token= sessionStorage.getItem("token");
                      this.domains[i].mS_Id= this.mS_Id;
                      }
                      var SZform =this.domains;
                      if (SZform.length>0){
                      this.SaveAddSZData(SZform);
                      }
                      this.alltableData = [];
                      for (var i = 0; i < data.length; i++) {
                          data[i].data.index = i;
                          data[i].data.xuhao = i+1;
                          if(data[i].data.dxts==""){
                           data[i].data.image="https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar";
                          }else{
                            if(data[i].path[0]==null){
                              data[i].data.image="https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar";
                            }else{
                              data[i].data.image =  this.$config.serverUrl+data[i].path[0].path || this.$config.serverUrl+data[i].path;
                            }
                          }
                          
                          this.alltableData.push(data[i].data);
                        }
                      this.total = data.length;
                      var index = Math.ceil(this.total / 10);
                      this.currentPage1 = index;
                      this.handleCurrentChange(index);
                    })
                    .catch(error => {
                      console.log(error);
                    });
                    this.$refs.form.resetFields();
                  });
                  this.$message.success(`新增第 ${this.index + 1} 行成功`);
                   this.dialogVisible = false;
              
              
            }
      })
    },
