<template>
    <div>
        <div class="upLoad">
            <el-upload
              :action="action"
			  class="upload-demo"
			  :data="fileData"
			  :before-upload="beforeUpload"
			  :on-success="uploadSuccess"
			  drag
			  multiple>
			  <i class="el-icon-upload"></i>
			  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
			  <!--<div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>-->
			</el-upload>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'upload',
        props:["XMBH","SCWJZL"],
        data: function(){
            return {
            	action:this.$config.serverUrl+ "api/ProjectInfo/Upload",
                defaultSrc: './static/img/noImg.jpg',
                fileList: [],
                fileData:{},
                imgSrc: '',
                cropImg: '',
                dialogVisible: false,
            }
        },
        methods:{
            beforeUpload(file) {
		        var S4=(((1+Math.random())*0x10000)|0).toString(16).substring(1);
		        var GUID=S4+S4+"-"+S4+"-"+S4+"-"+S4+"-"+S4+S4+S4;
		        this.fileData.GUID = this.SCWJZL || this.XMBH;
		     //   this.fileData.BQ_MC = "项目材料//"+ this.XMBH;   //目前暂不支持  --服务不支持
		        this.fileData.BQ_MC = "项目材料";   //目前暂不支持
		        this.fileData.Token=sessionStorage.getItem("token");
		    },
		    uploadSuccess(res,file,fileList){
		    	console.log(arguments);
		    }
        },
        created(){
            this.cropImg = this.defaultSrc;
        }
    }
</script>

<style scoped>
    .content-title{
        font-weight: 400;
        line-height: 50px;
        margin: 10px 0;
        font-size: 22px;
        color: #1f2f3d;
    }
    .pre-img{   
        width: 100px;
        height: 100px;
        background: #f8f8f8;
        border: 1px solid #eee;
        border-radius: 5px;
    }
    .crop-demo{
        display: flex;
        align-items: flex-end;
    }
    .crop-demo-btn{
        position: relative;
        width: 100px;
        height: 40px;
        line-height: 40px;
        padding: 0 20px;
        margin-left: 30px;
        background-color: #409eff;
        color: #fff;
        font-size: 14px;
        border-radius: 4px;
        box-sizing: border-box;
    }
    .crop-input{
        position: absolute;
        width: 100px;
        height: 40px;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;
    }
</style>


