        miaoMuSanDanJia: "",
        guanHuFei: "",
        keJiTuiGuangFei: "",
        guanLiFei: "",
        qiTaFeiYong: "",
        gC_Id: "",

        Token: "",
        index: ""
      },
      selVal: "",
      idx: -1
    };
  },
  beforeCreate() {
  },
  created() {
    this.GetData();
    this.GetMSData();
    this.GetSzData();
  },
  mounted() {},
  computed: {
    data() {
      return this.tableData;
    }
  },
  methods: {
    // 分页导航
    handleCurrentChange(val) {
      var length = 10;
      var start = length * (val - 1);
      var end = start + 10;
      this.tableData = this.alltableData.slice(start, end);
    },
    //选择造林树种
    currentJB(selVal) {
     
           this.GetSZData(selVal);
    },
    createStateFilter(queryString) {
        return (state) => {
          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect(item) {
        console.log(item);
      },
    addDomain() {
      this.domains.push({
          miaoMuDanJia: "",
          jjzB_ID: "",
          zlsZ_ID: "",
          Token: "",
          index: "",
          id: 0
        }
      );
    },
    removeDomain(item) {
        if(item.id!=0){
            var index = this.domains.indexOf(item)
              if (index !== -1) {
                this.domains.splice(index, 1)
                    }
                this.ids.push(item.id);
                
              }
            var index = this.domains.indexOf(item)
            if (index !== -1) {
              this.domains.splice(index, 1)
          }
      },
     querySearchAsync(queryString, callback) {
      var restaurants = this.restaurants;
      // 如果参数都没变化，则使用缓存数据，避免请求沉积 
            let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
            if(results.length>0){
                callback(results);
            }else {
              this.restaurants=[];
          //调用的后台接口
          this.$axios
            .post(
              this.$config.serverUrl + "api/YZL_JJZB/GetBiaoZhunData",
              JSON.stringify(queryString),
              {
                headers: {
                  "Content-Type": "application/json"
                }
              }
            )
            .then(res => {
              let data = res.data;
                if(data=="没有数据"){
                     var json = {
                    "value": "没有数据",
                  };
                  this.restaurants.push(json);
                  callback(this.restaurants);
                }
              for (var i = 0; i < data.length; i++) {
                  data[i].index = i;
                  data[i].xuhao = i+1;
                  var json = {
                    "value": data[i].biaoZhun,
                  };
                  this.restaurants.push(json);
                  callback(this.restaurants);
                }
            })
            .catch(error => {
              console.log(error);
               if(error=="没有数据"){
                     var json = {
                    "value": "没有数据",
                  };
                  this.restaurants.push(json);
                  callback(this.restaurants);
                }
            });
          
        } 
      },
 
    // 获取经济指标数据
    GetData() {
      this.$axios
        .post(this.$config.serverUrl + "api/YZL_JJZB/GetData", {
          headers: {
            "Content-Type": "application/json"
          }
        })
        .then(res => {
          let data = res.data;
          for (var i = 0; i < data.length; i++) {
            data[i].index = i;
            data[i].xuhao = i+1;
          }
          this.alltableData = [];
          this.alltableData = data;
          this.total = data.length;
          this.tableData = this.alltableData.slice(0, 10);
          this.currentPage1 = 1;
        })
        .catch(error => {
          console.log(error);
        });
    },
     // 获取造林模式数据
    GetMSData() {
      this.$axios
        .post(this.$config.serverUrl + "api/YZL_JJZB/GetMSData", {
          headers: {
            "Content-Type": "application/json"
          }
        })
        .then(res => {
          let data = res.data;
          this.zlmssoptions=data;
        })
        .catch(error => {
          console.log(error);
        });
    },
     // 获取造林树种数据
    GetSZData(id) {
      this.$axios
        .post(this.$config.serverUrl + "api/YZL_JJZB/GetSZData",  JSON.stringify(id),{
          headers: {
            "Content-Type": "application/json"
          }
        })
        .then(res => {
          let data = res.data;
          this.zlszsoptions=data;
        })
        .catch(error => {
          console.log(error);
        });
    },
     // 获取造林树种数据
    GetSzData() {
      this.$axios
        .post(this.$config.serverUrl + "api/YZL_JJZB/GetSData",{
          headers: {
            "Content-Type": "application/json"
          }
        })
