          let data = res.data;
          this.domains=[];
          this.domains = data;   
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 保存编辑经济指标数据
    SaveEditData(Form) {
      return  this.$axios
        .post(
          this.$config.serverUrl + "api/YZL_JJZB/SaveEditData",
          JSON.stringify(Form),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          let data = res.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
     // 保存编辑苗木单价数据
    SaveMMEditData(SZform) {
      this.$axios
        .post(
          this.$config.serverUrl + "api/YZL_JJZB/SaveMMEditData",
          JSON.stringify(SZform),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          let data = res.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    //新增经济指标数据
    SaveAddData(form) {
      return this.$axios
        .post(
          this.$config.serverUrl + "api/YZL_JJZB/SaveAddData",
          JSON.stringify(form),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .catch(error => {
          console.log(error);
        });
    },
     //新增苗木单价数据
    SaveAddMMData(MMform) {
      return this.$axios
        .post(
          this.$config.serverUrl + "api/YZL_JJZB/SaveAddMMData",
          JSON.stringify(MMform),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          this.domains=[];
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 删除数据
    delectEditData(id) {
      return this.$axios
        .post(
          this.$config.serverUrl + "api/YZL_JJZB/DelectEditData",
          JSON.stringify(id),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .catch(error => {
          console.log(error);
        });
    },
     // 删除苗木单价数据
    delectEditMMData(ids) {
      return this.$axios
        .post(
          this.$config.serverUrl + "api/YZL_JJZB/DelectEditMMData",
          JSON.stringify(ids),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .catch(error => {
          console.log(error);
        });
    },
    search() {
       var zqname=this.select_word;
       if(zqname==""){
         this.GetData();
       }else{
          this.$axios
        .post(
          this.$config.serverUrl + "api/YZL_JJZB/GetZQDataByName",
          JSON.stringify(zqname),
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          if (res.status) {
            let data = res.data;
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
            }
            this.alltableData = [];
            this.alltableData = data;
            this.total = data.length;
            var index = Math.ceil(this.total / 10);
            this.currentPage1 = index;
            this.handleCurrentChange(index);
            //this.tableData = this.alltableData.slice(0, 9);
          } else {
            this.$message.error(res.status);
          }
        })
        .catch(error => {
          console.log(error);
          //	this.$message.error(error);
          //callback(new Error(error));
        });
       }
      

    },
    handleEdit(index, row) {
      const item = this.tableData[index];
      this.Form =item;
      //this.Form.zlmS_ID=this.tableData[index].zlms_id_Name
       this.jjzB_ID=item.id;
      this.ids=[];
      this.GetMMData(item.id);
      this.editVisible = true;
    },
    handleAdd() {
      this.$refs.form.resetFields();
       this.zlszsoptions= [],
       this.zlmssoptions=  [],
       this.GetMSData();
       this.GetSzData();
      this.dialogVisible = true;
    },
    handleDelete(index, row) {
      this.idx = index;
      const item = this.tableData[index];
      this.Form = {
        id: item.id,
        index: item.index
      };
      this.delVisible = true;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 保存编辑
    saveEdit() {
      this.Form.Token = sessionStorage.getItem("token");
      for (var i=0;i<this.domains.length;i++){
            if(this.domains[i].miaoMuDanJia==""){
                        alert("苗木单价不能为空");
                          return false;
              }
            }
      this.SaveEditData(this.Form).then(res => {
        this.$axios
          .post(
            this.$config.serverUrl + "api/YZL_JJZB/GetData",
            {
              headers: {
                "Content-Type": "application/json"
              }
            }
          )
          .then(res => {
            let data = res.data;
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
            }
            this.alltableData = [];
            this.alltableData = data;
            this.total = data.length;
            var index = Math.ceil(this.Form.index / 10);
            this.currentPage1 = index;
            if(index==0){
                index=1
              }
            this.handleCurrentChange(index);
          })
          .catch(error => {
            console.log(error);
          });
      });
      //苗木单价
      var adddata=[];
      var addeditdata=[];
      for (var i=0;i<this.domains.length;i++){
        this.domains[i].Token= sessionStorage.getItem("token");
        this.domains[i].jjzB_ID= this.jjzB_ID;
        if(this.domains[i].id==0){
            adddata.push(this.domains[i]);
        }else{
          addeditdata.push(this.domains[i]);
        }

      }
      if(adddata.length>0){
          this.SaveAddMMData(adddata);
      }
      if(addeditdata.length>0){
          this.SaveMMEditData(addeditdata);
      }
       if(this.ids.length>0){
          this.delectEditMMData(this.ids);
      }
      this.$message.success(`修改第 ${this.idx + 1} 行成功`);
      this.editVisible = false;
      //
    },
    // 保存新增
    saveAdd() {
      this.form.Token = sessionStorage.getItem("token");
      for (var i=0;i<this.domains.length;i++){
            if(this.domains[i].miaoMuDanJia==""){
                        alert("苗木单价不能为空");
                          return false;
              }
            }
      this.SaveAddData(this.form).then(res => {
        this.$axios
          .post(
            this.$config.serverUrl + "api/YZL_JJZB/GetData",
            {
              headers: {
                "Content-Type": "application/json"
              }
            }
          )
          .then(res => {
            let data = res.data;
            this.jjzB_ID = Math.max.apply(
              Math,
              data.map(function(o) {
                return o.id;
              })
            );
            for (var i=0;i<this.domains.length;i++){
            this.domains[i].Token= sessionStorage.getItem("token");
            this.domains[i].jjzB_ID= this.jjzB_ID;
            }

            var MMform =this.domains;
            if (MMform.length>0){
             this.SaveAddMMData(MMform);
            }
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
            }
            this.alltableData = [];
            this.alltableData = data;
            this.total = data.length;
            var index = Math.ceil(this.total / 10);
            this.currentPage1 = index;
            this.handleCurrentChange(index);
          })
          .catch(error => {
            console.log(error);
          });
        this.$refs.form.resetFields();
      });
      this.$message.success(`新增第 ${this.idx + 1} 行成功`);

      this.dialogVisible = false;
    },
    // 确定删除
    deleteRow() {
      this.delectEditData(this.Form.id).then(res => {
        this.$axios
          .post(
            this.$config.serverUrl + "api/YZL_JJZB/GetData",
            {
              headers: {
                "Content-Type": "application/json"
              }
            }
          )
          .then(res => {
            let data = res.data;
            for (var i = 0; i < data.length; i++) {
              data[i].index = i;
              data[i].xuhao = i+1;
            }
            this.alltableData = [];
            this.alltableData = data;
            this.total = data.length;
            var index = Math.ceil(this.Form.index / 10);
            this.currentPage1 = index;
            this.handleCurrentChange(index);
          })
          .catch(error => {
            console.log(error);
          });
      });
      this.$message.success("删除成功");
      this.delVisible = false;
      //
    },
  }
};
</script>

<style scoped>
@import "../common/css/proCommon.css";
/************dialog Form input******************/
  .demo-drawer-footer{
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        border-top: 1px solid #e8e8e8;
        padding: 10px 16px;
        text-align: right;
        background: #fff;
    }
    .form_add {
  width: 380px;
  float: left;
  margin-bottom: 5px;
}
 .form_add1 {
  width: 300px;
  float: left;
  margin-bottom: 5px;
}
</style>


