<template>
    <div class="sidebar">
        <el-menu v-loading="loading"  @select=selected  type="primary" class="sidebar-el-menu" :default-active="onRoutes" :collapse="collapse" unique-opened router>
            <template v-for="item in menuList">
                <template v-if="item.children && item.children.length>0">
                    <el-submenu :index="item.path" :disabled="item.disable" :key="item.path">
                        <template slot="title" >
	                  
	                        <i :class="item.icon" ></i><span slot="title" >{{ item.name }}</span>

                        </template>
                        
                        <template v-for="subItem in item.children">
                            <el-submenu v-if="subItem.children && subItem.children.length>0" :disabled="subItem.disable" :index="subItem.path" :key="subItem.path">
                                <template slot="title">{{ subItem.name }}</template>
                                <el-menu-item  
                                	v-for="(threeItem,i) in subItem.children" 
                                	:key="i" 
                                	:disabled="threeItem.disable"
                                	:index="threeItem.path">
                                	<i v-if="threeItem.icon" :class = "threeItem.icon"></i>
                                    {{ threeItem.name }}
                                </el-menu-item>
                               
                            </el-submenu>
                            <el-menu-item v-else :index="subItem.path" :key="subItem.path">
                            	<i v-if="subItem.icon" :class = "subItem.icon"></i>
                                {{ subItem.name }}
                            </el-menu-item>
                            
                        </template>
                    </el-submenu>
                    
                </template>
                
                <template v-else>
                    <el-menu-item :disabled="item.disable" :index="`/${item.path}`" :key="item.path">
                        <i v-if="item.icon != null && item.icon !='' " :class="item.icon"></i>
                        <span slot="title">{{ item.name }}</span>
                    </el-menu-item> 
                </template>
            </template>
            <template>
            		<!--<el-menu-item index="SkywayCesium" key="SkywayCesium">
                        <i class="fa fa-globe"></i>
                        <span slot="title">三维电子沙盘</span>
                    </el-menu-item> -->
             		<!--<el-menu-item index="SubcompartmentWorkSpace" key="SubcompartmentWorkSpace">
                        <i class="fa fa-newspaper-o"></i>
                        <span slot="title">小班工作空间</span>
                    </el-menu-item> -->
                    <!--<el-menu-item index="ProjectOverview" key="ProjectOverview">
                        <i class="fa fa-newspaper-o"></i>
                        <span slot="title">工程概况</span>
                    </el-menu-item> 
                    <el-menu-item index="ForestDesign" key="ForestDesign">
                        <i class="fa fa-pagelines"></i>
                        <span slot="title">造林设计</span>
                    </el-menu-item>
                    <el-menu-item index="ForestApproval" key="ForestApproval">
                        <i class="fa fa-envelope-open-o"></i>
                        <span slot="title">工程审核</span>
                    </el-menu-item> 
                    <el-menu-item index="ForestAccept" key="ForestAccept">
                        <i class="fa fa-envelope"></i>
                        <span slot="title">工程验收</span>
                    </el-menu-item> 
                    <el-menu-item index="ForestCheck" key="ForestCheck">
                        <i class="fa fa-check"></i>
                        <span slot="title">工程检验</span>
                    </el-menu-item> 
                    <el-menu-item index="ForestPlan" key="ForestPlan">
                        <i class="fa fa-ravelry"></i>
                        <span slot="title">造林计划</span>
                    </el-menu-item> 
                    <el-menu-item index="ForestMode" key="ForestMode">
                        <i class="fa fa-eercast"></i>
                        <span slot="title">造林模式</span>
                    </el-menu-item> 
                     <el-menu-item index="PostalCode" key="PostalCode">
                        <i class="fa fa-cube"></i>
                        <span slot="title">政区代码</span>
                    </el-menu-item> 
                    <el-menu-item index="EconomicsIndex" key="EconomicsIndex">
                        <i class="fa fa-cny"></i>
                        <span slot="title">经济指标</span>
                    </el-menu-item> -->
            </template>
        </el-menu>
    </div>
</template>

<script>
    import bus from './bus';
    export default {
        data() {
            return {
            	loading:true,
                collapse: false,
                collapseBgColor:"#1fa67a",  //"#324157"---dark  "#1fa67a"--theme-green
                collapseTextColor:"#efefef", //"#bfcbd9"--dark
               // collapseTextActiveColor:"#20a0ff", //"#20a0ff" ---drak 
                menuList: [],
                /*[
                    {
                        icon: 'fa fa-home fa-2x',
                        path: 'dashboard',
                        name: '系统首页'
                    },
                    {
                        icon: 'fa fa-table',
                        path: 'table',
                        name: '基础表格'
                    },
                    {
                        icon: 'fa fa-copy',
                        path: 'tabs',
                        name: 'tab选项卡'
                    },
                    {
                        icon: 'fa fa-calendar',
                        path: '3',
                        name: '表单相关',
                        children: [
                            {
                                path: 'form',
                                name: '基本表单'
                            },
                            {
                                path: '3-2',
                                name: '三级菜单',
                                children: [
                                    {
                                        path: 'editor',
                                        name: '富文本编辑器'
                                    },
                                    {
                                        path: 'markdown',
                                        name: 'markdown编辑器'
                                    },
                                ]
                            },
                            {
                                path: 'upload',
                                name: '文件上传'
                            }
                        ]
                    },
                    {
                        icon: 'fa fa-smile-o',
                        path: 'icon',
                        name: '自定义图标'
                    },
                    {
                        icon: 'fa fa-pie-chart',
                        path: 'charts',
                        name: 'schart图表'
                    },
                    {
                        icon: 'fa fa-arrows',
                        path: 'drag',
                        name: '拖拽列表'
                    },
                    {
                        icon: 'fa fa-exclamation-triangle',
                        path: '6',
                        name: '错误处理',
                        children: [
                            {
                                path: 'permission',
                                name: '权限测试'
                            },
                            {
                                path: '404',
                                name: '404页面'
                            }
                        ]
                    }
                ]*/
            }
        },
        beforeCreate(){
			//console.log(this.$store.state.token);
			//console.log(this.$config.serverUrl);
			this.$Loading.start();
	//		console.log(JSON.stringify({token: this.$store.state.token,systemId: this.$config.systemID}));
			 this.$axios.post(this.$config.serverUrl + "/api/Module/GetModules",
			 				JSON.stringify({token: this.$store.state.token,
  											systemId: this.$config.systemID}),
							{headers: {'Content-Type': 'application/json'}}
                   			).then(res => {
							let data = res.data;
							this.menuList = data;
							console.log(data);
							this.loading = false;
							this.$Loading.finish();
						}).catch(error => {
							this.$Loading.error();
							this.$message.error(error);
							
						})
				
		},
        computed:{
            onRoutes(){
                return this.$route.path.replace('/','');
            }
        },
        created(){
            // 通过 Event Bus 进行组件间通信，来折叠侧边栏
            bus.$on('collapse', msg => {
                this.collapse = msg;
            })
        },
        methods:{
        	selected(path,indexPath){
        	//	console.log(path,indexPath);
        	}
        }
    }
</script>

<style scoped>
    .sidebar{
        display: block;
        position: absolute;
        left: 0;
        top: 70px;
        bottom:0;
        overflow-y: scroll;
    }
    .sidebar::-webkit-scrollbar{
        width: 0;
    }
    .sidebar-el-menu:not(.el-menu--collapse){
        width: 250px;
    }
    .sidebar > ul {
    	
        height:100%;
    }
    .sidebar ul.el-menu li>i,.sidebar ul.el-menu li>div>i{
    	vertical-align: middle;
	    margin-right: 5px;
	    width: 18px;
	    text-align: center;
	    font-size: 18px;
    }
    .sidebar ul.el-menu li>span,.sidebar ul.el-menu li>div>span{
    	font-size: 16px;
    }
	.sidebar ul.el-menu li>div>span{
    	margin-left:4px;
    }
</style>



